import React, { useState, useRef, createContext, useContext, useCallback,useEffect } from "react";
import { createRoot } from 'react-dom/client';
import "./PopupError.scss";


let timeoutId = null;

const ErrorPopup = ({ message }) => {
  const [isVisible, setIsVisible] = useState(true);

  useEffect(() => {
    // Set the fade out effect after 5 seconds
    timeoutId = setTimeout(() => {
      setIsVisible(false);
    }, 5000);

    // Clear the timeout if the component is unmounted
    return () => {
      clearTimeout(timeoutId);
    };
  }, []);

  const handleCloseClick = () => {
    clearTimeout(timeoutId);
    setIsVisible(false);
  };

  if (!isVisible) {
    return null;
  }

  return (
    <div className={`error-popup ${!isVisible ? 'fade-out' : ''}`}>
      <div>{message}</div>
      <div className="close-icon" onClick={handleCloseClick}></div>
    </div>
  );
}

export default function popupError(message) {
  const popupElement = document.createElement('div');
  document.body.appendChild(popupElement);

  // ReactDOM.render(<ErrorPopup message={message} />, popupElement);

  const root = createRoot(popupElement); // Create a root.
  root.render(<ErrorPopup message={message} />);

  // Remove the popup from the DOM after it fades out
  setTimeout(() => {
    root.unmount();
    document.body.removeChild(popupElement);
    // ReactDOM.unmountComponentAtNode(popupElement);
    // document.body.removeChild(popupElement);
  }, 6000); // slightly longer to ensure the animation completes
}
