import { MEASURE_TYPE_NUMBER } from "../models/Measure";
import { useDays } from "../store/days";
import { useMeasures } from "../store/measures";
import { useTasks } from "../store/tasks";
import { getDOWShortText } from "../utils/time";
import InteractiveChart from "./InteractiveChart";

const format = time => {
  const date = new Date(time)
  const dow = getDOWShortText(date)
  // const day
  return dow
}

export default function () {
  console.debug("auto chart redraw")

  const days = useDays()
  const tasks = useTasks()
  const { getMeasureValueByTaskIdAndDayId } = useMeasures()

  const countAt = dayId => {
    let s = 0
    for (const task of tasks) {
      const m = getMeasureValueByTaskIdAndDayId(task.id, dayId)
      if (m.type === MEASURE_TYPE_NUMBER) {
        s += Math.max(0, Math.min(1, m.value))
      }
    }
    return s
  }

  const xValues = []
  const yValues = []
  const xLabels = []
  const yLabels = []

  for (const day of days.slice(-28)) {
    const count = countAt(day.id)
    xValues.push(format(day.time))
    // xValues.push(xValues.length)
    yValues.push(count)
  }

  return (
    <div>
      {/* <Chart
        width={window.innerWidth}
        height={window.innerWidth / 2}
        xValues={xValues}
        yValues={yValues}
        xLabels={xLabels}
        yLabels={yLabels}
        thickness={2}
        color="blue"
      /> */}
      <InteractiveChart
        xValues={xValues}
        yValues={yValues}
      />
    </div>
  )
}