const RotatingSegment = ({ image1, image2, lineAngle, segmentAngle }) => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    const img1 = new Image();
    const img2 = new Image();
    img1.src = image1;
    img2.src = image2;

    img1.onload = () => {
      img2.onload = () => {
        drawImages(context, img1, img2);
      };
    };

    const drawImages = (ctx, img1, img2) => {
      const width = canvas.width;
      const height = canvas.height;
      const centerX = width / 2;
      const centerY = height / 2;

      // Draw the first image covering the entire canvas
      ctx.drawImage(img1, 0, 0, width, height);

      // Set up the clipping region for the segment
      ctx.save();
      ctx.beginPath();
      ctx.moveTo(centerX, centerY);
      ctx.arc(centerX, centerY, Math.min(centerX, centerY) * 2, (lineAngle * Math.PI) / 180, ((lineAngle + segmentAngle) * Math.PI) / 180);
      ctx.closePath();
      ctx.clip();

      // Draw the second image within the clipping region
      ctx.drawImage(img2, 0, 0, width, height);
      ctx.restore();
    };
  }, [image1, image2, lineAngle, segmentAngle]);

  return (
    <canvas ref={canvasRef} width={500} height={500} />
  );
}

function DayNightCircle() {
  const image1 = process.env.PUBLIC_URL + "/image1.png"
  const image2 = process.env.PUBLIC_URL + "/image2.png"
  const image3 = process.env.PUBLIC_URL + "/image3.png"
  const image4 = process.env.PUBLIC_URL + "/image4.png"

  const { now } = useNow(100)
  const a = now
  return <DayNightSegments
    images={[image1, image2, image3, image4]}
    angles={[
      ((-10 * a / 1000) % 360) + 0,
      ((-10 * a / 1000) % 360) + 30,
      ((-10 * a / 1000) % 360) + 180,
      ((-10 * a / 1000) % 360) + 270,
    ]}
    transitionWidth={1}
  />
}

const DayNightSegments = ({ images, angles, transitionWidth }) => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    const imageElements = images.map((src) => {
      const img = new Image();
      img.src = src;
      return img;
    });

    Promise.all(imageElements.map(img => new Promise(resolve => img.onload = resolve)))
      .then(() => {
        drawImages(context, imageElements, angles, transitionWidth);
      });

    const drawImages = (ctx, imageElements, angles, transitionWidth) => {
      const width = canvas.width;
      const height = canvas.height;
      const centerX = width / 2;
      const centerY = height / 2;
      const segmentAngle = 360 / imageElements.length;

      imageElements.forEach((image, index) => {
        ctx.save();
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);
        ctx.arc(centerX, centerY, Math.min(centerX, centerY), (angles[index] * Math.PI) / 180, ((angles[index] + segmentAngle) * Math.PI) / 180);
        ctx.closePath();
        ctx.clip();

        ctx.drawImage(image, 0, 0, width, height);
        ctx.restore();
      });

      // Draw blend transitions between segments
      angles.forEach((angle, index) => {
        const nextIndex = (index + 1) % imageElements.length;
        const nextAngle = angles[nextIndex];

        ctx.save();
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);

        // Draw gradient blend for current segment
        const gradient = ctx.createLinearGradient(
          centerX + Math.cos((angle * Math.PI) / 180) * Math.min(centerX, centerY),
          centerY + Math.sin((angle * Math.PI) / 180) * Math.min(centerX, centerY),
          centerX + Math.cos((nextAngle * Math.PI) / 180) * Math.min(centerX, centerY),
          centerY + Math.sin((nextAngle * Math.PI) / 180) * Math.min(centerX, centerY)
        );

        gradient.addColorStop(0, 'rgba(255,255,255, 0)');
        gradient.addColorStop(1, 'rgba(255,255,255, 1)');

        ctx.strokeStyle = gradient;
        ctx.lineWidth = transitionWidth;

        ctx.moveTo(centerX, centerY);
        ctx.lineTo(
          centerX + Math.cos((angle * Math.PI) / 180) * Math.min(centerX, centerY),
          centerY + Math.sin((angle * Math.PI) / 180) * Math.min(centerX, centerY)
        );
        ctx.stroke();
        ctx.restore();
      });
    };
  }, [images, angles, transitionWidth]);

  return (
    <canvas ref={canvasRef} width={500} height={500} />
  );
}


