import React, { useState } from 'react';
import { loadStateFromLocalStorage } from '../store';

function LocalStorageManager() {
    const [fileName, setFileName] = useState('localStorageData.json');

    // Save localStorage to a file
    const saveLocalStorageToFile = () => {
        const data = JSON.stringify(localStorage, null, 2);
        const blob = new Blob([data], { type: 'application/json' });
        const a = document.createElement('a');
        a.href = URL.createObjectURL(blob);
        a.download = fileName;
        a.click();
    };

    // Load localStorage from a file
    const loadLocalStorageFromFile = (event) => {
        const file = event.target.files[0];
        const reader = new FileReader();

        reader.onload = function (e) {
            try {
                const data = JSON.parse(e.target.result);
                localStorage.clear(); // Optionally clear existing localStorage
                Object.keys(data).forEach(key => {
                    localStorage.setItem(key, data[key]);
                });
                window.location = process.env.PUBLIC_URL || "/" 
            } catch (error) {
                console.error('Error loading localStorage:', error);
                alert('There was an error loading the file.');
            }
        };

        reader.readAsText(file);
    };

    return (
        <div>
            <h3>LocalStorage Manager</h3>

            {/* Save localStorage to file */}
            <div>
                <input
                    type="text"
                    value={fileName}
                    onChange={(e) => setFileName(e.target.value)}
                    placeholder="Enter file name"
                />
                <button onClick={saveLocalStorageToFile}>Save localStorage to file</button>
            </div>

            {/* Load localStorage from file */}
            <div>
                <input type="file" onChange={loadLocalStorageFromFile} />
            </div>
        </div>
    );
}

export default LocalStorageManager;
