import React from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

// Register required chart.js components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const Chart = ({ xValues, yValues }) => {
  // Data and configuration for the chart
  const data = {
    labels: xValues,
    datasets: [
      {
        label: 'Data',
        data: yValues,
        borderColor: 'rgba(75, 192, 192, 1)',
        backgroundColor: 'rgba(75, 192, 192, 0.2)',
        borderWidth: 2,
        tension: 0.3,  // Optional, for smooth curve lines
      }
    ]
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        display: false,
        position: 'top',
      },
      tooltip: {
        enabled: true,
      }
    },
    scales: {
      x: {
        title: {
          display: false,
          text: 'X Axis'
        }
      },
      y: {
        title: {
          display: true,
          text: 'FINISHED TASKS'
        }
      }
    }
  };

  return <Line data={data} options={options} />;
};

export default Chart;
