import { MEASURE_TYPE_EMPTY, MEASURE_TYPE_NUMBER } from "../models/Measure"
import { useMeasures } from "../store/measures"
import { useDays } from "./days"

const formatPercent = x => x == /** approx */ 0 ? 0 : Math.floor(x * 100) + "%";

export function useStats() {
  const days = useDays()
  const { getMeasureValueByTaskIdAndDayId } = useMeasures()

  function getFreq(taskId, PERIOD) {
    let s = 0
    if (days.length === 0) {
      return 0
    }
    const todayId = days[days.length - 1].id
    const todayMeasure = getMeasureValueByTaskIdAndDayId(taskId, todayId)
    const haveTodayMeasure = todayMeasure.type === MEASURE_TYPE_NUMBER

    let lastDays
    if (haveTodayMeasure) {
      lastDays = days.slice(-PERIOD)
    } else {
      lastDays = days.slice(-PERIOD - 1, -1)
    }

    let count = 0
    for (const { id } of lastDays) {
      const m = getMeasureValueByTaskIdAndDayId(taskId, id)
      if (m.type === MEASURE_TYPE_NUMBER) {
        s += Math.max(0, Math.min(1, m.value))
        count++
      } else if (m.type === MEASURE_TYPE_EMPTY) {
        count++
      }
    }

    return s / count || 0
  }

  return {
    total(taskId) {
      let s = 0
      for (const { id } of days) {
        s += (getMeasureValueByTaskIdAndDayId(taskId, id).value || 0)
      }
      return s
    },
    freq28(taskId) {
      return formatPercent(getFreq(taskId, 28))
    },
    freq7(taskId) {
      return formatPercent(getFreq(taskId, 7))
    },
  }

}  