import { useDispatch, useSelector } from "react-redux"
import { resetSystemStartTime, updateAllTasks } from "../store"
import { useEffect, useRef, useState } from "react"
import ChangeDateModal from "../components/ChangeDateModal"
import Navigation from "../components/Navigation"
import { Collapsible, CollapsibleSwitcher, CollapsibleBodyOpened, CollapsibleBodyClosed } from '../components/Collapsible'
import { Swipeable, SwipeableRightAction } from "../components/Swipeable"
import { useTasks } from "../store/tasks"
import AutoTable from "../components/AutoTable"
import Accordion from "../components/Accordion"
import { Draggable } from "../components/Pointer"
import AutoDayMeter from "../components/AutoDayMeter"
import AutoChart from "../components/AutoChart"

const DAY = 60 * 60 * 24 * 1000

export default function () {
  const dispatch = useDispatch()

  // const system = useSelector(state => state.system)
  console.debug("table-page redraw")

  const ref = useRef()
  const startTime = useSelector(state => state.system.startTime)
  const settings = useSelector(state => state.system.settings) || {}
  const tasks = useTasks()

  const dayMap = useSelector(state => state.dayMap)
  const dayIndexToTime = index => index * DAY + startTime

  const [dateChangeModal, setDateChangeModal] = useState({ show: false, dayId: null })

  const [scrollTop, setScrollTop] = useState(0)
  const [prevScrollTop, setPrevScrollTop] = useState(0)

  const showingTasks = tasks.filter(t => !t.isHidden)
  const hiddenTasks = tasks.filter(t => t.isHidden)

  let maxDaysToShow = 100500
  if (typeof settings.maxDaysToShow === "number") {
    if (0 < settings.maxDaysToShow && settings.maxDaysToShow <= 100500) {
      maxDaysToShow = settings.maxDaysToShow
    }
  }

  function getClasses() {
    let wrapClassName = ""

    if (settings.grayOutDoneTasks) {
      wrapClassName += "gray-out-done-tasks "
    }

    if (settings.colorStats) {
      wrapClassName += "color-stats "

      if (settings.colorStatsExceptZeros) {
        wrapClassName += "color-stats-except-zeros "
      }
    }

    const cls = `h-100 border-box ${wrapClassName}`

    return cls
  }

  return (
    <div id="table-page" className={getClasses()} ref={ref}>
      <Draggable
        minYOffset={10}
        maxXOffset={10}
        onDragStart={() => {
          setPrevScrollTop(document.documentElement.scrollTop)
        }}
        onDragMove={({ detail: { offset } }) => {
          document.documentElement.scrollTop = prevScrollTop - offset.y
          setScrollTop(document.documentElement.scrollTop)
        }}
        onDragEnd={() => {
        }}
      >
        <Navigation style={{ opacity: Math.min(1, Math.max(0, 20 - scrollTop) / 20) }} />

        <Accordion>
          <AutoDayMeter />
          <AutoChart />
        </Accordion>

        <AutoTable tasks={showingTasks} showTaskHide={true} />

        {
          (hiddenTasks.length > 0) &&
          <Collapsible>
            <CollapsibleBodyOpened>
              <CollapsibleSwitcher>
                <Swipeable>
                  <div className="w-100 bb" style={{ background: "white", border: "1px solid white" }}>
                    <h4>[-] Hidden</h4>
                  </div>
                  <SwipeableRightAction width={40} onAction={() => dispatch(updateAllTasks({ isHidden: false }))}>
                    Unhide all
                  </SwipeableRightAction>
                </Swipeable>
              </CollapsibleSwitcher>
              <AutoTable tasks={hiddenTasks} showTaskUnhide={true} />
            </CollapsibleBodyOpened>
            <CollapsibleBodyClosed>
              <CollapsibleSwitcher>
                <Swipeable>
                  <div className="w-100 bb" style={{ background: "white", border: "1px solid white" }}>
                    <h4>[+] Hidden ({hiddenTasks.length})</h4>
                  </div>
                  <SwipeableRightAction width={40} onAction={() => dispatch(updateAllTasks({ isHidden: false }))}>
                    Unhide all
                  </SwipeableRightAction>
                </Swipeable>
              </CollapsibleSwitcher>
            </CollapsibleBodyClosed>
          </Collapsible>
        }

      </Draggable>


      <ChangeDateModal
        show={dateChangeModal.show}
        initialValue={dayIndexToTime(dayMap[dateChangeModal.dayId] && dayMap[dateChangeModal.dayId].index)}
        onChange={(newDate, oldDate) => dispatch(resetSystemStartTime(startTime + newDate.getTime() - oldDate.getTime()))}
        onClose={() => setDateChangeModal({ show: false })}
      />
    </div>
  )
}