import React, { useEffect, useRef, useState } from "react"
// import useDrag from "../hooks/useDrag"
import "./Overlay.scss"
import ReactDOM from 'react-dom'
import { hasParentWithClass } from "../utils/html"
import { Draggable } from "./Pointer"

export default function ({
  minXOffset = 10,
  maxYOffset = 10,
  minYOffset = undefined,
  maxXOffset = undefined,
  base,
  overlay,
  onSelect = button => button.click(),
}) {
  const capturedRef = useRef()
  const [isDragging, setDragging] = useState(false)

  function setCaptured(el) {
    const captured = capturedRef.current

    if (captured) {
      if (el) {
        if (el === captured) {
          // do nothing
        } else {
          captured.classList.remove("hover")
          el.classList.add("hover")
          capturedRef.current = el
        }
      } else {
        captured.classList.remove("hover")
        capturedRef.current = null
      }
    } else {
      if (el) {
        el.classList.add("hover")
        capturedRef.current = el
      } else {
        capturedRef.current = null
      }
    }
  }

  const [rect, setRect] = useState({
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
  })

  const ref = useRef()

  const overlayStyle = {
    display: isDragging ? 'block' : 'none',
    position: 'fixed', // Use fixed to position relative to the viewport
    top: `${rect.top}px`,
    left: `${rect.left}px`,
  }

  const overlayContent = isDragging ? (
    ReactDOM.createPortal(
      <div className="overlay-top" style={overlayStyle}>
        {overlay}
      </div>,
      document.body // Attach to body
    )
  ) : null

  function handleDragStart() {
    console.log("drag start")
    setRect(ref.current.getBoundingClientRect())
    setDragging(true)
  }

  function handleDragMove({ detail: { currentPoint } }) {
    console.log("drag move")
    const el = document.elementFromPoint(currentPoint.x, currentPoint.y)
    if (el && hasParentWithClass(el, "capture-overlay")) {
      console.debug("capture", el)
      setCaptured(el)
    } else {
      setCaptured(null)
    }
  }

  function handleDragEnd() {
    console.log("drag end")
    setDragging(false)
    console.debug("selected", capturedRef.current)
    capturedRef.current && onSelect(capturedRef.current)
  }

  return (
    <div className="overlay-wrapper">
      <Draggable
        minXOffset={minXOffset}
        maxYOffset={maxYOffset}
        minYOffset={minYOffset}
        maxXOffset={maxXOffset}
        onDragMove={handleDragMove}
        onDragStart={handleDragStart}
        onDragEnd={handleDragEnd}
      >
        <div ref={ref} className="overlay-base">
          {base}
        </div>
      </Draggable>
      {overlayContent}
    </div>
  )
}

export function CaptureOverlay({ children }) {
  return (
    <div className="capture-overlay">
      {children}
    </div>
  )
}