import { createAsyncThunk } from '@reduxjs/toolkit'
import { createSlice } from '@reduxjs/toolkit'
import { configureStore } from '@reduxjs/toolkit'

import { MEASURE_TYPE_EMPTY, MEASURE_TYPE_NA, MEASURE_TYPE_NUMBER, Measure } from '../models/Measure'
import uuidv4 from '../utils/uuidv4'

// Function to save the Redux state to localStorage
const saveStateToLocalStorage = (state) => {
  console.debug("saving to local storage")
  try {
    const serializedState = JSON.stringify(state)
    localStorage.setItem('reduxState', serializedState)
  } catch (err) {
    console.error('Error saving state to localStorage:', err)
  }
}

// Function to load the Redux state from localStorage
const loadStateFromLocalStorage = () => {
  try {
    const serializedState = localStorage.getItem('reduxState')
    if (serializedState === null) {
      return undefined
    }
    return JSON.parse(serializedState)
  } catch (err) {
    console.error('Error loading state from localStorage:', err)
    if (window.confirm("Local storage is broken. Do we start from clean plate?")) {
      return undefined
    } else {
      throw err
    }
  }
}

const dayMap = createSlice({
  name: 'dayMap',
  initialState: {
    0: {
      id: 0,
      index: 0,
    },
    1: {
      id: 1,
      index: 1,
    },
  },
  reducers: {
    addDay(state, { payload: { index } }) {
      const ix = Object.values(state).map(o => o.index)
      if (ix.length > 0) {
        const left = Math.min(...ix)
        const right = Math.max(...ix)
        if (index !== left - 1 && index !== right + 1) {
          throw Error(`Can only add a new day at the head or at the tail`)
        }
      }
      state[index] = { id: index, index }
    },
    delDay(state, { payload: { id } }) {
      const index = state[id].index
      const ix = Object.values(state).map(o => o.index)
      if (ix.length !== 0) {
        const left = Math.min(...ix)
        const right = Math.max(...ix)
        if (index !== left && index !== right) {
          throw Error(`Can only remove head or tail day`)
        }
      } else {
        throw Error(`No days to remove`)
      }
      delete state[id]
    },
  },
})

export const { addDay, delDay } = dayMap.actions

const measureMap = createSlice({
  name: 'measureMap',
  initialState: {
    "id1.0": {
      id: "id1.0",
      value: 123,
    },
    "id1.1": {
      id: "id1.1",
      value: 122,
    },
  },
  reducers: {
    setMeasure(state, { payload }) {
      const measure = Measure.parse(payload)
      state[measure.id] = measure.data()
    },
    incMeasure(state, { payload: { id, taskId, dayId, value } }) {
      if (typeof value !== "number") {
        console.error(`incMeasure: expected a number, but got ${value} (maybe you wanted to use setMeasure()?)`)
        throw Error("Not a number")
      }

      if (id === undefined) {
        id = Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })
      }

      const m = state[id]

      let prev = 0
      if (m && m.value) {
        prev = m.value
      }

      state[id] = new Measure({ id, type: MEASURE_TYPE_NUMBER, value: Math.max(0, prev + value) }).data()
    },
    clearMeasure(state, { payload: id }) {
      /* do not raise error */
      delete state[id]
    },
    clearAllByDayId(state, { payload: dayId }) {
      console.debug("removing measures...")
      for (const m of Object.values(state)) {
        /* compare string with number */
        if (new Measure(m).dayId == dayId) {
          console.debug("remove measure", m.id)
          delete state[m.id]
        }
      }
    }
  },
})

export const { setMeasure, incMeasure, clearMeasure, clearAllByDayId } = measureMap.actions

const taskMap = createSlice({
  name: 'taskMap',
  initialState: {
    id1: {
      id: "id1",
      title: "Hello Task 1",
      reward: 100,
      index: 0,
      isHidden: false,
      created: new Date().getTime(),
    },
    id2: {
      id: "id2",
      title: "Hello Task 2",
      reward: 100,
      index: 1,
      isHidden: false,
      created: new Date().getTime(),
    },
    id3: {
      id: "id3",
      title: "Hello Task 3",
      reward: 999,
      index: 2,
      isHidden: false,
      created: new Date().getTime(),
    },
  },
  reducers: {
    collapseTaskGroup(state, { payload: { id, isCollapsed } }) {
      const items = Object.values(state).sort((a, b) => a.index - b.index)
      for (let i = 0; i < items.length; i++) {
        if (items[i].id === id) {
          items[i].isCollapsed = isCollapsed
          for (let j = i + 1; j < items.length && !items[j].isGroup; j++) {
            items[j].isCollapsed = isCollapsed
          }
          return
        }
      }
    },
    newTask(state, { payload: { title, created, isGroup, group } }) {

      let index
      if (group) {
        index = group.index + 0.5
      } else {
        index = Math.max(...Object.values(state).map(t => t.index)) + 1
      }

      if (!created) {
        throw Error(`task.created expected`)
      }

      const task = {
        id: uuidv4(),
        isGroup,
        title: title || "",
        reward: 0,
        index,
        created,
      }

      state[task.id] = task

      Object.values(state).sort((a, b) => a.index - b.index).forEach((item, index) => {
        item.index = index
      })
    },
    moveTaskGroup(state, { payload: {
      sliceFrom,
      count,
      indexToAfterSplice
    } }) {

      const items = Object.values(state).sort((a, b) => a.index - b.index)
      const itemsToMove = items.splice(sliceFrom, count)
      // console.debug("ItemsToMove", itemsToMove.map(t=>t.title))

      items.splice(indexToAfterSplice, 0, ...itemsToMove)
      // console.debug(items.map(t => [t.title, t.index]))

      items.forEach((item, index) => item.index = index)
    },
    moveTask(state, { payload: { indexFrom, indexTo } }) {
      console.debug("moveTask", indexFrom, indexTo)
      
      let taskIdToMove
      for (const [taskId, task] of Object.entries(state)) {
        if (task.index === indexFrom) {
          taskIdToMove = taskId
          break
        }
      }

      if (taskIdToMove) {
        const taskIds = Object.keys(state).sort((a, b) => state[a].index - state[b].index)
        const currentIndex = taskIds.indexOf(taskIdToMove)

        if (currentIndex !== -1) {
          taskIds.splice(currentIndex, 1)
          taskIds.splice(indexTo, 0, taskIdToMove)

          taskIds.forEach((taskId, index) => {
            state[taskId].index = index
          })
        }
      }
    },
    removeTask(state, { payload: { id } }) {
      delete state[id]
      Object
        .values(state)
        .sort((a, b) => a.index - b.index)
        .forEach((t, i) => t.index = i)
    },
    updateTask(state, { payload }) {
      const task = state[payload.id]
      for (const key in payload) {
        task[key] = payload[key]
      }
    },
    updateAllTasks(state, { payload }) {
      for (const task of Object.values(state)) {
        for (const key in payload) {
          task[key] = payload[key]
        }
      }
    },
    deleteTaskGroup(state, { payload }) {

      const items = Object
        .values(state)
        .sort((a, b) => a.index - b.index)

      delete state[payload.id]

      let i = 0
      while (i < items.length && items[i].id !== payload.id) {
        i++
      }

      i++

      while (!items[i].isGroup) {
        delete state[items[i].id]
        i++
      }

      Object
        .values(state)
        .sort((a, b) => a.index - b.index)
        .forEach((t, i) => t.index = i)

    },
    updateTaskGroup(state, { payload }) {

      function update(item) {
        for (const key in payload) {
          if (key !== "id") {
            item[key] = payload[key]
          }
        }
      }

      const items = Object
        .values(state)
        .sort((a, b) => a.index - b.index)

      update(state[payload.id])

      let i = 0

      while (i < items.length && items[i].id !== payload.id) {
        i++
      }

      i++

      while (!items[i].isGroup) {
        update(state[items[i].id])
        i++
      }

    },
    reorderTasks(state, { payload: taskIds }) {
      taskIds.forEach((taskId, index) => {
        state[taskId].index = index
      })
    }
  },
})

export const { moveTask, reorderTasks, updateTaskGroup, deleteTaskGroup, collapseTaskGroup, removeTask, updateTask, updateAllTasks, moveTaskGroup } = taskMap.actions

const table = createSlice({
  name: 'table',
  initialState: {
    showDatesTo: null,
    showDatesCount: 0,
  },
  reducers: {
    setDates(state, { payload: { showDatesTo, showDatesCount } }) {
      state.showDatesCount = showDatesCount
      state.showDatesTo = showDatesTo
    },
  },
})

export const { setDates } = table.actions

const system = createSlice({
  name: 'system',
  initialState: {
    storageVersion: 0,
    time: 0,
    statId: "freq7",
    startTime: new Date().getTime(),
    grayOutDoneTasks: false,
    autoReward: false,
    settings: {},
  },
  reducers: {
    initSettings(state) {
      if (!state.settings) {
        state.settings = {}
      }
    },
    updateSetting(state, { payload: { key, value } }) {
      state.settings[key] = value
    },
    setStorageVersion(state, { payload: version }) {
      state.storageVersion = version
    },
    fetchSystemTime(state) {
      state.time = new Date().getTime()
    },
    setSystemStatId(state, { payload: statId }) {
      state.statId = statId
    },
    resetSystemStartTime(state, { payload: newTime }) {
      state.startTime = newTime
    },
  },
})

export const {
  setStorageVersion,
  fetchSystemTime,
  resetSystemStartTime,
  setSystemStatId,
  setSystemPoints,
  updateSetting,
} = system.actions

const timer = createSlice({
  name: 'timer',
  initialState: {
    time: 0,
    isRunning: false,
    startTime: null,
  },
  reducers: {
    startTimer(state, { payload: startTime }) {
      if (!startTime) {
        throw Error(`start time is not specified`)
      }
      state.isRunning = true
      state.startTime = startTime
    },
    pauseTimer(state, { payload: stopTime }) {
      const elapsed = stopTime - state.startTime
      state.isRunning = false
      state.time -= elapsed
    },
    setTimer(state, { payload: time }) {
      state.time = time
    },
    incTimer(state, { payload: time }) {
      state.time += time
    },
  },
})

export const { startTimer, pauseTimer, incTimer, setTimer } = timer.actions

const persistedState = loadStateFromLocalStorage()


export const store = configureStore({
  reducer: {
    dayMap: dayMap.reducer,
    taskMap: taskMap.reducer,
    measureMap: measureMap.reducer,
    system: system.reducer,
    table: table.reducer,
    timer: timer.reducer,
  },
  preloadedState: persistedState,
})


store.subscribe(() => {
  saveStateToLocalStorage(store.getState())
})



export const removeDayAndMeasuresByDayId = createAsyncThunk(
  'dayMap/removeDayAndMeasures',
  async (dayId, { dispatch, getState }) => {

    const state = getState()
    const index = state.dayMap[dayId].index
    const ix = Object.values(state.dayMap).map(o => o.index)
    const maxIndex = Math.max(...ix)
    const minIndex = Math.min(...ix)

    if (index !== maxIndex && index !== minIndex) {
      console.error(`Only edge dates can be removed`)
      throw Error(`Only edge dates can be removed`)
    }

    // First, remove all measures for the specific date
    Object
      .keys(state.measureMap)
      .filter(key => key.endsWith(`.${index}`))
      .forEach(key => dispatch(clearMeasure(key)))

    // Then, remove the day itself
    dispatch(delDay({ id: dayId }))
  }
)

// Usage example:
// dispatch(removeDayAndMeasures(1700524800000))

export const setMeasuresForAllTasksByDayId = createAsyncThunk(
  'taskMap/setOrCreateMeasuresForDayForAllTasks',
  async ({ dayId, value }, { dispatch, getState }) => {
    const state = getState()
    const tasks = state.taskMap

    Object.keys(tasks).forEach(taskId => {
      const measureId = `${taskId}.${dayId}`

      if (typeof value === "function") {
        const m = state.measureMap[measureId]

        try {
          dispatch(setMeasure({ id: measureId, value: value(m && m.value) }))
        } catch (e) {
          console.error(e)
        }
      }
      else if (value === undefined || value === "") {
        dispatch(clearMeasure(measureId))
      } else {
        dispatch(setMeasure({ id: measureId, value }))
      }
    })
  }
)

// Usage example:
// dispatch(setOrCreateMeasuresForDayForAllTasks({ dayTime: 1700524800000, value: 123 }))
export const moveFinishedTasksToTop = createAsyncThunk(
  'tasks/moveToTop',
  async (_, { dispatch, getState }) => {
    console.debug("tasks/moveToTop")

    const { measureMap, dayMap, taskMap } = getState()
    const days = Object.values(dayMap).sort((a, b) => a.index - b.index)
    if (days.length === 0) {
      return
    }

    const lastDayId = days[days.length - 1].id
    const getLastMeasure = taskId => measureMap[Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId: lastDayId })]
    const isNotMeasured = taskId => getLastMeasure(taskId) === undefined || getLastMeasure(taskId).type === "empty"

    const tasks = Object.values(taskMap).sort((a, b) => a.index - b.index)
    const swap = (i, j) => {
      const t = tasks[i]
      tasks[i] = tasks[j]
      tasks[j] = t
    }

    for (let i = 0; i < tasks.length; i++) {
      for (let j = 1; j < tasks.length; j++) {
        const a = tasks[j - 1]
        const b = tasks[j]
        if (a.isGroup || b.isGroup) {
          continue
        }
        if (isNotMeasured(a.id) && !isNotMeasured(b.id)) {
          swap(j - 1, j)
        }
      }
    }

    dispatch(reorderTasks(tasks.map(t => t.id)))
  }
)

export const newTask = createAsyncThunk(
  'task/new',
  async (task, { dispatch, getState }) => {
    if (!task.created) {
      task.created = Date.now()
    }
    dispatch(taskMap.actions.newTask(task))
  }
)

export const migrateStorage = createAsyncThunk(
  'storage/migrate',
  async (_, { dispatch, getState }) => {
    let v = getState().system.storageVersion

    if (!v) {
      // version 0 => 1
      console.warn(`Updating storage version ${v} => 1`)
      const state = getState()

      Object.values(state.taskMap).forEach(task => {
        if (task.created === undefined) {
          dispatch(updateTask({
            id: task.id,
            created: state.system.startTime,
          }))
        }
      })

      dispatch(setStorageVersion(v = 1))
    }
    if (v === 1) {
      // v1 => v2
      console.warn(`Updating storage version ${v} => 2`)
      const state = getState()

      Object.values(state.measureMap).forEach(m => {
        let type
        if (m.value === null) {
          type = MEASURE_TYPE_NA
        } else if (m.value === undefined) {
          type = MEASURE_TYPE_EMPTY
        } else if (typeof m.value === "number") {
          type = MEASURE_TYPE_NUMBER
        } else {
          console.error("Couldn't migrate measure", m)
          return
        }
        dispatch(setMeasure({
          id: m.id,
          value: m.value,
          type,
        }))
      })

      dispatch(setStorageVersion(v = 2))
    }
    if (v === 2) {
      console.warn(`Updating storage version ${v} => 3`)
      dispatch(system.actions.initSettings())
      dispatch(setStorageVersion(v = 3))
    }
  }
)