import { useCallback, useEffect, useRef, useState } from "react"
import Scrollable from "./Scrollable"
import useResize from "../hooks/useResize"

export default function ({
    scrollLeft,
    initRight,
    rightContent,
    leftContent,
    children,
    onRightAction,
    onLeftAction,
    onScrollLeft,
    ...args
}) {
    const ref = useRef()
    const rightContentRef = useRef()
    const leftContentRef = useRef()
    const [scrollLeftBorder, setScrollLeftBorder] = useState()
    const [scrollRightBorder, setScrollRightBorder] = useState()
    const [rightWidth, setRightWidth] = useState()
    const [leftWidth, setLeftWidth] = useState()
    const [isInitialized, setInitialized] = useState(false)
    const [isSizesReady, setSizesReady] = useState(false)
    const [stickRight, setStickRight] = useState(initRight)

    function getUnwrappedScrollLeft(x) {
        if (!leftWidth || !scrollRightBorder) {
            return 0
        }
        if (x < leftWidth) {
            return 0
        }
        if (x > scrollRightBorder) {
            return scrollRightBorder - leftWidth
        }
        return x - leftWidth
    }

    function up(x) {
        setStickRight(x >= scrollRightBorder)
        onScrollLeft(x, getUnwrappedScrollLeft(x))
    }

    function updateSize() {
        if (!ref.current || !rightContentRef.current || !leftContentRef.current) {
            return
        }

        const totalWidth = ref.current.offsetWidth
        if (!totalWidth) {
            return
        }

        const rightRect = rightContentRef.current.getBoundingClientRect()
        const rightWidth = rightRect.width
        if (!rightWidth) {
            return
        }

        const leftRect = leftContentRef.current.getBoundingClientRect()
        const leftWidth = leftRect.width
        if (!leftWidth) {
            return
        }

        const totalScrollWidth = ref.current.scrollWidth
        const maxScrollWidth = totalScrollWidth - totalWidth

        const scrollLeftBorder = leftWidth
        const scrollRightBorder = maxScrollWidth - rightWidth

        setRightWidth(rightWidth)
        setLeftWidth(leftWidth)
        setScrollLeftBorder(scrollLeftBorder)
        setScrollRightBorder(scrollRightBorder)
        setSizesReady(true)
    }

    useEffect(() => {
        if (!isSizesReady) {
            return
        }
        setInitialized(true)
        if (!isInitialized && initRight) {
            up(scrollRightBorder)
        } else if (args.scrollLeft < scrollLeftBorder) {
            up(0)
        } else if (args.scrollLeft > scrollRightBorder) {
            up(scrollRightBorder)
        } else if (stickRight) {
            up(scrollRightBorder)
        }
    }, [isSizesReady, scrollRightBorder])

    useResize(ref, updateSize)
    useResize(rightContentRef, updateSize)
    useResize(leftContentRef, updateSize)

    return (
        <Scrollable
            onStopScroll={({ scrollLeft }) => {
                if (scrollLeft > scrollRightBorder) {
                    if (scrollLeft > scrollRightBorder + rightWidth * 0.98) {
                        onRightAction()
                    }
                    up(scrollRightBorder)

                } else if (scrollLeft < scrollLeftBorder) {
                    if (scrollLeft < scrollLeftBorder - leftWidth * 0.98) {
                        onLeftAction()
                    }
                    up(scrollLeftBorder)
                }
            }}
            scrollLeft={scrollLeft}
            onScrollLeft={up}
            {...args}
        >
            <div ref={ref} style={{ display: "flex" }}>
                <div ref={leftContentRef}> {/* Add ref to leftContent */}
                    {leftContent}
                </div>
                <div>
                    {children}
                </div>
                <div ref={rightContentRef}>
                    {rightContent}
                </div>
            </div>
        </Scrollable>
    )
}