export function hasParentWithClass(element, className) {
  while (element) {
    if (element.classList && element.classList.contains(className)) {
      return true;
    }
    // console.debug("^", element.classList)
    // Move up to the next parent element
    element = element.parentElement;
  }
  return false; // No parent with the specified class name found
}

export function findParentWithClass(element, className) {
  while (element) {
    if (element.classList && element.classList.contains(className)) {
      return element;
    }
    element = element.parentElement;
  }
  return null
}

export function findAllParentsWithClass(element, className) {
  const result = []
  while (element) {
    if (element.classList && element.classList.contains(className)) {
      result.push(element)
    }
    element = element.parentElement;
  }
  return result
}

export function getAllParents(element) {
  const parents = []
  while (element) {
    parents.push(element)
    element = element.parentElement;
  }
  return parents;
}
