import { useDispatch, useSelector } from "react-redux"
import { incTimer, pauseTimer, setTimer, startTimer } from "../store"
import DayMeter from "../components/DayMeter"
import useNow from "../hooks/useNow"

export default function () {
  const dispatch = useDispatch()
  const { now: time } = useNow()
  const timer = useSelector(state => state.timer)

  const timerElapsed = timer.isRunning ? time - timer.startTime : 0
  const timerTime = timer.time - timerElapsed

  return (
    <DayMeter
      value={parseInt(timerTime / 60000)}
      subValue={timer.isRunning ? ("" + Math.abs(timerTime % 60000)).padStart(5, "0") : ""}
      isRunning={timer.isRunning}
      onChange={value => dispatch(setTimer(value * 60000))}
      onInc={value => dispatch(incTimer(value * 60000))}
      onPlay={() => dispatch(startTimer(time))}
      onPause={() => dispatch(pauseTimer(time))}
    />
  )
}