import { Sortable } from "./Sortable";

export default function ({
  style,
  children,
  isSimpleItemIndex,
  findGroupIndexFromIndex,
  onSimpleItemMove,
  onGroupMove,
  onStart,
  onFinish,
}) {

  function onMove(indexFrom, indexTo) {

    if (isSimpleItemIndex(indexFrom)) {

      console.debug("simple move")
      onSimpleItemMove(indexFrom, indexTo)

    } else {

      console.debug("group move")
      const sliceFrom = indexFrom
      const sliceTo = findGroupIndexFromIndex(indexFrom + 1)

      if (sliceTo <= indexTo) {
        indexTo = findGroupIndexFromIndex(indexTo + 1)
      } else if (indexTo < sliceFrom) {
        indexTo = findGroupIndexFromIndex(indexTo)
      } else {
        console.error("fail", sliceFrom, sliceTo, indexTo)
        return
      }

      if (sliceFrom === indexTo) {
        console.debug("idle move")
        return
      }

      const count = sliceTo - sliceFrom
      const spliceDelta = sliceFrom < indexTo ? - count : 0;
      const indexToAfterSplice = indexTo + spliceDelta

      console.debug(`Moving [${sliceFrom}..${sliceTo}](${count}) -> ${indexTo} (${indexToAfterSplice})`)

      onGroupMove({
        spliceDelta,
        sliceFrom,
        sliceTo,
        indexTo,
        count,
        indexToAfterSplice,
      })

    }

  }

  return (
    <Sortable style={style} onMove={onMove} onStart={onStart} onFinish={onFinish}>
      {children}
    </Sortable>
  )
}