import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import { store } from './store';
import { Provider } from 'react-redux';
import { PointerProvider } from './components/Pointer';


function hashCode(str) {
  let hash = 0;
  for (let i = 0, len = str.length; i < len; i++) {
    let chr = str.charCodeAt(i);
    hash = (hash << 5) - hash + chr;
    hash |= 0; // Convert to 32bit integer
  }
  return hash;
}

const hash = hashCode(window.localStorage.password || window.location.search.slice(1))

if (hash == 68170072) {
  if (!window.localStorage.password) {
    window.localStorage.password = window.location.search.slice(1)
  }

  ReactDOM.createRoot(document.getElementById('root')).render(
    <React.StrictMode>
      <PointerProvider>
        <Provider store={store}>
          <App />
        </Provider>
      </PointerProvider>
    </React.StrictMode>
  )
}

