import React, { useState, useContext } from 'react';

// Context to share the collapsible state and toggler function
const CollapsibleContext = React.createContext();

const Collapsible = ({ initialShow, children }) => {
  const [isOpen, setIsOpen] = useState(initialShow); // Default open state

  const toggleOpen = () => setIsOpen(!isOpen);

  return (
    <CollapsibleContext.Provider value={{ isOpen, toggleOpen }}>
      {children}
    </CollapsibleContext.Provider>
  );
};

const CollapsibleSwitcher = ({ children }) => {
  const { toggleOpen } = useContext(CollapsibleContext);
  return <div onClick={toggleOpen}>{children}</div>;
};

const CollapsibleBodyOpened = ({ children }) => {
  const { isOpen } = useContext(CollapsibleContext);
  return isOpen ? <div>{children}</div> : null;
};

const CollapsibleBodyClosed = ({ children }) => {
  const { isOpen } = useContext(CollapsibleContext);
  return !isOpen ? <div>{children}</div> : null;
};

export { Collapsible, CollapsibleSwitcher, CollapsibleBodyOpened, CollapsibleBodyClosed };
