import { Link } from 'react-router-dom';
import React, { useState } from 'react';
import "./Navigation.scss"

export default function Navigation({ style }) {
  const [isOpen, setIsOpen] = useState(false)
  const className = `top-nav ${isOpen && 'active'}`

  return (
    <div className='top-nav-wrapper' style={style}>
      <nav className={className}>
        <button className='top-nav-bread' onClick={() => setIsOpen(!isOpen)}>
          &#9776;
        </button>
        {
          isOpen
          &&
          <ul>
            <li onClick={() => setIsOpen(false)}><Link to={`${process.env.PUBLIC_URL}/`}>Table</Link></li>
            <li onClick={() => setIsOpen(false)}><Link to={`${process.env.PUBLIC_URL}/test`}>Test</Link></li>
            <li onClick={() => setIsOpen(false)}><Link to={`${process.env.PUBLIC_URL}/settings`}>Settings</Link></li>
          </ul>
        }
      </nav>
    </div>
  );
}
