import React from 'react';

function limit(x, a, b) {
  if (x < a) {
    return a
  }
  if (x > b) {
    return b
  }
  return x
}

const GaugeMeter = ({
  min,
  max,
  value,
  ticks,
  style,
  fireColor = "white",
  animate,
}) => {
  value = limit(value, min, max)

  if (typeof min !== "number") {
    throw Error(`GaugeMeter.min: expected a number, got ${min}`)
  }
  if (typeof max !== "number") {
    throw Error(`GaugeMeter.max: expected a number, got ${max}`)
  }
  if (typeof value !== "number") {
    throw Error(`GaugeMeter.value: expected a number, got ${value}`)
  }

  const scale = (max - min)

  function value2degree(value) {
    return (value - min) / scale * 180 - 90
  }
  // Convert value to a degree (0 - 180 degrees for this gauge)
  const degree = value2degree(value);

  // SVG rotation for the needle, pivot point at the center of the gauge
  const needleRotation = `rotate(${degree} 50 50)`;

  return (
    <svg viewBox="0 0 100 50" style={style}>

      {/* Define filters */}
      <defs>

        <filter id="textShadowFilter" x="-50%" y="-50%" width="200%" height="200%">
          <feTurbulence baseFrequency="0.01 0.24" result="NOISE" numOctaves="2" />
          <feDisplacementMap in="SourceGraphic" in2="NOISE" scale="2" xChannelSelector="R" yChannelSelector="R">
            <animate attributeName="scale" values="2;1.8;2" dur={animate ? "1s" : "1000000s"} repeatCount="indefinite" />
          </feDisplacementMap>
          {/* <feDropShadow dx="0" dy="0" stdDeviation="2" floodColor={fireColor} /> */}
        </filter>

        <filter id="shadowFilter" x="-50%" y="-50%" width="200%" height="200%">
          <feDropShadow dx="0" dy="0" stdDeviation="2" floodColor={fireColor} />
          <feDropShadow dx="0" dy="0" stdDeviation="2" floodColor={fireColor} />
          <feDropShadow dx="0" dy="0" stdDeviation="2" floodColor={fireColor} />
        </filter>

        <filter id="arrowFilter" x="-50%" y="-50%" width="200%" height="200%">
          <feDropShadow dx="0" dy="0" stdDeviation="2" floodColor={fireColor} />
          <feDropShadow dx="0" dy="0" stdDeviation="2" floodColor={fireColor} />
        </filter>

        {/* Grainy texture filter with animation */}
        <filter id="grainyTexture">
          <feTurbulence type="fractalNoise" baseFrequency="0.8" numOctaves="2" stitchTiles="stitch">
            <animate attributeName="baseFrequency" values="0.5;0.6;0.7;0.8;0.7;0.6;0.5" dur={animate ? "2s" : "1000000s"} repeatCount="indefinite" />
          </feTurbulence>
          <feDisplacementMap in="SourceGraphic" scale="5" />
        </filter>

        <filter id="lightGrainyTexture">
          <feTurbulence type="fractalNoise" baseFrequency="0.8" numOctaves="3" stitchTiles="stitch">
            <animate attributeName="baseFrequency" values="0.5;0.6;0.7;0.8;0.7;0.6;0.5" dur={animate ? "2s" : "1000000s"} repeatCount="indefinite" />
          </feTurbulence>
          <feDisplacementMap in="SourceGraphic" scale="0.25" />
        </filter>
      </defs>

      {/* Gauge semi-circle */}
      <path d="M 2,50 A 40,40 0 0,1 98,50" fill="none" stroke="black" strokeWidth="2" />

      <defs>
        <filter id="glowing" x="-50%" y="-50%" width="200%" height="200%" filterUnits="userSpaceOnUse">
          <feGaussianBlur in="SourceAlpha" stdDeviation="2" result="coloredBlur" />
          <feOffset dx="0" dy="0" result="offsetblur" />
          <feMerge>
            <feMergeNode in="coloredBlur" />
            <feMergeNode in="SourceGraphic" />
          </feMerge>
        </filter>
      </defs>
      <line x1="0" y1="50" x2="100" y2="50" stroke="black" strokeWidth="2" filter="url(#glowing) url(#glowing) url(#glowing)"></line>

      {/* Ticks and Numbers */}
      {
        ticks.map(({ value, tickType, label, color }, index) => {
          const tickDegree = value2degree(value)
          // const tick = {
          //   large: { x1: 50, y1: 1, x2: 50, y2: 6, strokeWidth: "1" },
          //   small: { x1: 50, y1: 1, x2: 50, y2: 4, strokeWidth: "0.5" }
          // }
          return (
            <g key={index} transform={`rotate(${tickDegree} 50 50)`}>
              {/* <line {...tick[tickType]} stroke="black" /> */}
              <polygon points="49 1, 51 1, 50 6" fill="black" />
              {tickType === 'large' && (
                <text
                  x="50"
                  y="10"
                  fill={color || "black"}
                  fontSize="3"
                  textAnchor="middle"
                  transform={`rotate(${-tickDegree} 50 10)`}
                  filter="url(#textShadowFilter)"
                >
                  {label || value}
                </text>
              )}
            </g>
          )
        })
      }
      {/* Needle */}
      <g transform={needleRotation}>
        <path d="M 45,50
               Q 50,40 50,20
               Q 50,40 55,50
               Z"
          fill="white"
          stroke={fireColor}
          strokeWidth="0.1"
          filter="url(#arrowFilter) url(#lightGrainyTexture)"
        />

        {/* <polygon points="50 20, 51.5 50, 48.5 50" fill={fireColor}  />
        <polygon points="50 21, 50.25 50, 49.75 50" fill="black" filter="url(#arrowFilter)" /> */}
        {/* <line x1={50} y1={30} x2={49.5 + 0.2} y2={40} stroke="black" strokeWidth="0.5" filter="url(#shadowFilter)" />
        <line x1={50} y1={30} x2={50.5 - 0.2} y2={40} stroke="black" strokeWidth="0.5" filter="url(#shadowFilter)" /> */}
      </g>

      {/* Center circle */}
      <circle cx="50" cy="50" r="10" fill="rgba(255,255,255,0.5)" stroke={fireColor} strokeWidth="0.5" filter="url(#grainyTexture) url(#shadowFilter)" />

      {/* bottom line */}
      <line x1="15" y1="50" x2="85" y2="50" stroke="black" strokeWidth="2" />

    </svg>
  )
}

export default GaugeMeter;