import React, { useState } from 'react'
import Navigation from '../components/Navigation'
import Switch from '../components/Switch'
import { useDispatch, useSelector } from 'react-redux'
import { updateSetting } from '../store'
import CustomInput from '../components/CustomInput'
import LazyInput from '../components/LazyInput'
import LocalStorageManager from '../components/LocalStorageManager'

export default function () {

  const settings = window.$settings = useSelector(state => state.system.settings)
  const dispatch = useDispatch()

  return (
    <div>

      <Navigation />

      <br />
      <br />
      <br />

      <div className='d-flex'>
        <Switch value={settings.grayOutDoneTasks} onChange={value => dispatch(updateSetting({ key: "grayOutDoneTasks", value }))} />
        <div>
          Gray out done tasks
        </div>
      </div>

      <br />

      <div className='d-flex'>
        <Switch value={settings.colorStats} onChange={value => dispatch(updateSetting({ key: "colorStats", value }))} />
        <div>
          Color stats
        </div>
      </div>

      {
        settings.colorStats
        &&
        (
          <div>
            <br />

            <div className='d-flex'>
              <Switch value={settings.colorStatsExceptZeros} onChange={value => dispatch(updateSetting({ key: "colorStatsExceptZeros", value }))} />
              <div>
                Color stats except zeros
              </div>
            </div>
          </div>
        )
      }
      <div>
        <br />
        <div className='d-flex'>
          <Switch value={settings.hideDone} onChange={value => dispatch(updateSetting({ key: "hideDone", value }))} />
          <div>
            Hide finished tasks
          </div>
        </div>
      </div>
      <div>
        <br />
        <div className='d-flex'>
          <Switch value={settings.moveDoneToTop} onChange={value => dispatch(updateSetting({ key: "moveDoneToTop", value }))} />
          <div>
            Move finished tasks to top of the group
          </div>
        </div>
      </div>
      <div>
        <br />
        <div className='d-flex'>
          <Switch value={settings.autoReward} onChange={value => dispatch(updateSetting({ key: "autoReward", value }))} />
          <div>
            Auto reward
          </div>
        </div>
      </div>
      {
        settings.autoReward
        &&
        (
          <div>
            <br />

            <div className='d-flex'>
              <div>
                Reward today only measures
              </div>
              <Switch value={!settings.rewardAnyDay} onChange={value => dispatch(updateSetting({ key: "rewardAnyDay", value: !value }))} />
            </div>
            <br />

            <div className='d-flex'>
              <div>
                Mean
              </div>
              <LazyInput type="number" value={settings.meanReward} onChange={value => dispatch(updateSetting({ key: "meanReward", value }))} />
            </div>
            <br />

            <div className='d-flex'>
              <div>
                Deviation
              </div>
              <LazyInput type="number" value={settings.deviationReward} onChange={value => dispatch(updateSetting({ key: "deviationReward", value }))} />
            </div>
          </div>
        )
      }
      <br />
      <br />
      <div className='d-flex'>
        <div>Max days to show:</div>
        <CustomInput
          style={{
            border: "1px solid black"
          }}
          value={settings.maxDaysToShow}
          onChange={e => dispatch(updateSetting({
            key: "maxDaysToShow",
            value: (parseInt(e.target.value) || undefined)
          }))}
        />
      </div>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />

      <LocalStorageManager />

      <hr />

      <button onClick={() => {
        if (window.confirm("r u sure?")) {
          localStorage.clear()
          window.location = process.env.PUBLIC_URL
        }
      }}>
        Clear storage
      </button>

    </div>
  )
}
