import CustomInput from "./CustomInput"
import Overlay from "./Overlay";
import CustomButton from "./CustomButton"
import "./CellAction.scss"
import "./CellActionConditions.scss"
import "./CellColor.scss"
import "./Cell.scss"
import LongClickInput from "./LongClickInput";

export function CellAction({
  className,
  onAction,
  children,
}) {
  return (
    <CustomButton onClick={onAction} className={`cell-action capture-overlay ${className}`}>
      {children}
    </CustomButton>
  )
}

export default function ({
  value,
  className,
  onChange,
  children,
}) {
  console.debug("cell redraw")
  
  return (
    <div style={{ position: "relative" }}>
      <Overlay
        base={
          <div className={`cell cell-color ${className}`}>
            <LongClickInput value={value} onChange={value => onChange(value)} />
          </div>
        }
        overlay={
          <div className="cell-actions">
            {children}
          </div>
        }
      />
    </div>
  )
}
