import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom'
import './ContextMenu.scss' // Assuming this is the path to your styles


export function ContextMenuItem({ onClick, children }) {
    return (
        <div className="context-menu-item" onClick={onClick}>
            {children}
        </div>
    )
}

export function ContextMenuXY({ isShowing, x, y, children, onClickOutside }) {
    const [menuElement] = useState(document.createElement('div'))

    useEffect(() => {
        document.body.appendChild(menuElement)
        return () => {
            document.body.removeChild(menuElement)
        }
    }, [menuElement])

    useEffect(() => {
        if (isShowing) {
            menuElement.style.position = `absolute`
            menuElement.style.left = `${x}px`
            menuElement.style.top = `${y}px`

            // Function to handle click or touch outside the menu
            const handleClickOutside = (event) => {
                if (menuElement && !menuElement.contains(event.target)) {
                    onClickOutside && onClickOutside(event) // Call the onClickOutside prop if defined
                }
            }

            // Add event listeners for clicks and touches
            document.addEventListener('mousedown', handleClickOutside)
            document.addEventListener('touchstart', handleClickOutside)

            // Cleanup function to remove event listeners
            return () => {
                document.removeEventListener('mousedown', handleClickOutside)
                document.removeEventListener('touchstart', handleClickOutside)
            }
        }
    }, [isShowing, x, y, menuElement, onClickOutside]) // Include onClickOutside in the dependency array

    const menuContent = (
        <div className={`context-menu ${isShowing ? 'show' : ''}`}>
            {children}
        </div>
    )

    return ReactDOM.createPortal(menuContent, menuElement)
}

export function ContextMenuTarget({ children }) {
    return (
        <div>
            {children}
        </div>
    )
}

function getBottomLeftCoordinates(node) {
    if (!node) {
        return { x: 0, y: 0 };
    }

    // Get the bounding rectangle of the node
    const rect = node.getBoundingClientRect();

    // Calculate the bottom-left coordinates
    // rect.left gives the left position
    // rect.top + rect.height gives the bottom position
    const x = rect.left + window.pageXOffset;
    const y = rect.top + rect.height + window.pageYOffset;

    return { x, y };
}

export function ContextMenu({ children }) {
    // State to manage the visibility and position of the context menu
    const [contextMenu, setContextMenu] = useState({
        isShowing: false,
        x: 0,
        y: 0,
    })

    // Function to display the context menu
    const handleContextMenu = (event) => {
        event.preventDefault() // Prevent the default context menu
        setContextMenu({
            isShowing: true,
            ...getBottomLeftCoordinates(event.target),
            // x: event.pageX,
            // y: event.pageY,
        })
    }

    // Function to hide the context menu
    const handleCloseContextMenu = () => {
        setContextMenu({
            ...contextMenu,
            isShowing: false,
        })
    }

    // // Example function to be called when a context menu item is clicked
    // const handleMenuItemClick = () => {
    //     handleCloseContextMenu() // Close the context menu after the action
    // }

    if (!(children instanceof Array)) {
        return children
    }

    const main = children.find(o => o.type === ContextMenuTarget)
    const items = children.filter(o => o !== main)

    if (items.length === 0) {
        return main
    }

    return (
        <div onContextMenu={handleContextMenu} onClick={handleCloseContextMenu}>
            <div className={contextMenu.isShowing ? 'context-menu-is-showing' : 'context-menu-is-not-showing'}>
                {main}
            </div>
            <ContextMenuXY isShowing={contextMenu.isShowing} x={contextMenu.x} y={contextMenu.y} onClickOutside={handleCloseContextMenu}>
                {items}
            </ContextMenuXY>
        </div>
    )
}