export class Stats {
  constructor({ id, title }) {
    this.id = id
    this.title = title
  }

  data() {
    return { ...this }
  }

  validate() {
    if (this.id === undefined) {
      throw Error()
    }
    if (this.title === undefined) {
      throw Error()
    }
  }
}


export const stats = [
  {
    id: "total",
    title: "TOTAL",
  },
  {
    id: "freq28",
    title: "FREQ MONTH",
  },
  {
    id: "freq7",
    title: "FREQ WEEK",
  },
].map(data => new Stats(data))