const DAY = 60 * 60 * 24 * 1000


export const MEASURE_TYPE_NA = "na"
export const MEASURE_TYPE_EMPTY = "empty"
export const MEASURE_TYPE_NUMBER = "number"

export class Measure {
    static getMeasureIdByTaskIdAndDayId({ taskId, dayId }) {
        if (taskId === undefined || dayId === undefined) {
            throw Error(`undefined`)
        }
        return `${taskId}.${dayId}`
    }

    static parse({ id, taskId, dayId, type, value }) {
        if (id === undefined) {
            id = this.getMeasureIdByTaskIdAndDayId({taskId, dayId})
        }

        if (type === undefined) {
            if (value === null) {
                type = MEASURE_TYPE_NA
                value = undefined
            } else if (value === undefined) {
                type = MEASURE_TYPE_EMPTY
                value = undefined
            } else if (value === "") {
                type = MEASURE_TYPE_EMPTY
                value = undefined
            } else if (typeof value === "string") {
                type = MEASURE_TYPE_NUMBER
                value = Math.abs(parseFloat(value))
                if (isNaN(value)) {
                    throw Error(`setMeasure: isNaN: ${value}`)
                }
            } else if (typeof value === "number") {
                type = MEASURE_TYPE_NUMBER
                if (isNaN(value)) {
                    throw Error(`setMeasure: isNaN: ${value}`)
                }
            } else {
                throw Error(`setMeasure: wrong value: ${value}`)
            }
        }

        switch (type) {
            case MEASURE_TYPE_EMPTY:
                break
            case MEASURE_TYPE_NA:
                break
            case MEASURE_TYPE_NUMBER:
                if (typeof value !== "number" || isNaN(value)) {
                    throw Error("MEASURE_TYPE_NUMBER: Not a number")
                }
                break
            default:
                throw Error("Unknown measure type: " + type)
        }

        return new Measure({ id, type, value })
    }

    validate() {
        if (this.id === undefined) {
            throw Error(`measure.id undefined`)
        }
        if (this.type === undefined) {
            throw Error(`measure.type undefined`)
        }
        if (this.value === undefined) {
            // ok
        } else if (this.value === null) {
            // ok
        } else if (typeof this.value !== "number" || isNaN(this.value)) {
            throw Error(`measure.value is nan`)
        }
        if (this.taskId === undefined) {
            throw Error(`measure.taskId undefined`)
        }
        if (this.dayId === undefined) {
            throw Error(`measure.dayId undefined`)
        }
    }

    data() {
        return {...this}
    }

    constructor({
        id,
        type,
        value,
    }) {
        this.id = id
        this.type = type
        this.value = value

        this.validate()
    }

    get taskId() {
        return this.id.split(".")[0]
    }

    get dayId() {
        return this.id.split(".")[1]
    }
}