import { useEffect, useState } from "react";
import ModalWindow from "./ModalWindow";

export default function ({ show, initialValue, onChange, onClose }) {
    const [value, setValue] = useState("")

    function handleOK() {
        onChange(parse(value), parse(initialValue))
        onClose()
    }

    function format(s) {
        if (typeof s === "number" || s instanceof Date) {
            const date = new Date(s)
            const yyyy = date.getFullYear()
            const mm = (1 + date.getMonth()).toString().padStart(2, "0")
            const dd = date.getDate().toString().padStart(2, "0")
            return `${yyyy}-${mm}-${dd}`
        }
        return s || ""
    }

    function parse(s) {
        return new Date(s)
    }

    useEffect(() => {
        setValue(format(initialValue))
    }, [show])

    return (
        <>
            {
                show &&

                <ModalWindow>
                    <input type="date" value={value} onChange={e => setValue(e.target.value)} />
                    <hr />
                    <button onClick={handleOK}>OK</button>
                    <button onClick={onClose}>Cancel</button>
                </ModalWindow>
            }
        </>
    )
}