import CustomInput from "./CustomInput"
import "./LazyInput.scss"

export default function({onChange, className, type, ...args}) {
    function handleBlur(e) {
        let value = e.target.value
        if (type === "number") {
            value = parseFloat(value) || 0
        }
        onChange && onChange(value)
    }
    return <CustomInput {...args} type={type} className={`lazy-input ${args.className}`} onBlur={handleBlur}/>
}