export function moved(items, indexFrom, toIndex) {
    // First, we'll make a copy of the items array to avoid mutations
    let itemsCopy = [...items];

    // Ensure indexes are within bounds
    if (indexFrom < 0 || indexFrom >= items.length || toIndex < 0 || toIndex >= items.length) {
        console.warn('Index out of bounds in move operation');
        return itemsCopy;
    }

    // Next, we'll splice out the item from its current position
    const [movedItem] = itemsCopy.splice(indexFrom, 1);

    // Then, we'll splice the item back into the desired position
    itemsCopy.splice(toIndex, 0, movedItem);

    // Return the new array with the item moved
    return itemsCopy;
}
