import React from 'react'
import './ArrowLeft.scss'

export default function ({
  caption,
  strokeWidth = 40,
  strokeColor = "lightgray",
  numberOfArrows = 5,
}) {
  const arrows = Array.from({ length: numberOfArrows })

  return (
    <div className="rectangle">
      <svg className="arrow" viewBox={`0 0 ${100 * numberOfArrows} 100`} preserveAspectRatio="none">
        {arrows.map((_, index) => (
          <polyline
            key={index}
            points={`100,-${strokeWidth} 50,50 100,${100 + strokeWidth}`} // Adjusted points for leftward arrow
            fill="none"
            stroke={strokeColor}
            strokeWidth={strokeWidth}
            transform={`translate(${100 * index},0)`}
          />
        ))}
      </svg>
      <div className="caption">{caption}</div>
    </div>
  )
}
