import { useDispatch, useSelector } from "react-redux"
import { addDay, clearAllByDayId, clearMeasure, collapseTaskGroup, deleteTaskGroup, fetchSystemTime, incMeasure, incTimer, moveFinishedTasksToTop, moveTask, moveTaskGroup, newTask, pauseTimer, removeDayAndMeasuresByDayId, removeTask, resetSystemStartTime, setMeasure, setMeasuresForAllTasksByDayId, setSystemPoints, setSystemStatId, setTimer, startTimer, updateAllTasks, updateTask, updateTaskGroup } from "../store"
import Table from "../components/Table"
import { useEffect, useRef, useState } from "react"
import { Measure } from "../models/Measure"
import { stats } from "../models/Stats"
import { useMeasures } from "../store/measures"
import { useStats } from "../store/stats"

const DAY = 60 * 60 * 24 * 1000
const toList = map => Object.values(map).sort((a, b) => a.index - b.index)

export default function ({
  tasks,
  showTaskHide,
  showTaskUnhide,
}) {
  console.debug("redraw auto-table")

  const dispatch = useDispatch()

  const startTime = useSelector(state => state.system.startTime)
  const settings = useSelector(state => state.system.settings)
  const statId = useSelector(state => state.system.statId)

  const dayMap = useSelector(state => state.dayMap)
  const dayIndexToTime = index => index * DAY + startTime
  const days = toList(dayMap).map(o => ({ ...o, time: dayIndexToTime(o.index) }))

  const maxDayTime = days.length > 0 && days[days.length - 1].time || 0
  const maxDayIndex = days.length > 0 ? days[days.length - 1].index : null

  const { getMeasureValueByTaskIdAndDayId } = useMeasures()
  const statFunctions = useStats()

  const getStatValueByTaskId = statFunctions[statId]

  const getNextDayIndex = () => (days.length > 0 ? days[days.length - 1].index + 1 : 0)
  const getPrevDayIndex = () => (days.length > 0 ? days[0].index - 1 : 0)

  // TODO
  // const [dateChangeModal, setDateChangeModal] = useState({ show: false, dayId: null })

  let maxDaysToShow = 3
  if (typeof settings.maxDaysToShow === "number") {
    if (0 < settings.maxDaysToShow && settings.maxDaysToShow <= 100500) {
      maxDaysToShow = settings.maxDaysToShow
    }
  }

  function afterMeasure({ taskId, dayId, value }) {
    if (settings.moveDoneToTop) {
      dispatch(moveFinishedTasksToTop())
    }
  }

  return (
    <Table
      settings={settings}
      tasks={tasks}
      days={days.slice(-maxDaysToShow)}
      stats={stats}
      currentStatId={statId}
      setGroupCollapsed={(groupId, isCollapsed) => dispatch(collapseTaskGroup({ id: groupId, isCollapsed }))}
      onStatChange={id => dispatch(setSystemStatId(id))}
      maxDayTime={days[days.length - 1].time}
      minDayTime={days[0].time}
      getMeasureValueByTaskIdAndDayId={getMeasureValueByTaskIdAndDayId}
      getStatValueByTaskId={getStatValueByTaskId}
      onNewTask={(title, group) => dispatch(newTask({ title, created: maxDayTime, group }))}
      onNewTaskGroup={title => dispatch(newTask({ title, isGroup: true, created: maxDayTime }))}
      onTaskUpdate={task => dispatch(updateTask(task))}
      onTaskDelete={task => dispatch(removeTask(task))}
      onTaskHide={task => dispatch(updateTask({ id: task.id, isHidden: true }))}
      onTaskUnhide={task => dispatch(updateTask({ id: task.id, isHidden: false }))}
      showTaskHide={showTaskHide}
      showTaskUnhide={showTaskUnhide}

      onTaskGroupDelete={group => dispatch(deleteTaskGroup({ id: group.id }))}
      onTaskGroupHide={group => dispatch(updateTaskGroup({ id: group.id, isHidden: true }))}
      onTaskGroupUnhide={group => dispatch(updateTaskGroup({ id: group.id, isHidden: false }))}

      onTaskMove={(unresolvedIndexFrom, unresolvedIndexTo) => dispatch(moveTask({ indexFrom: tasks[unresolvedIndexFrom].index, indexTo: tasks[unresolvedIndexTo].index }))}
      onTaskGroupMove={unresolved => {
        const sliceFrom = tasks[unresolved.sliceFrom].index

        let indexToAfterSplice = unresolved.indexTo
        if (unresolved.indexTo < tasks.length) {
          indexToAfterSplice = tasks[unresolved.indexTo].index
        }
        indexToAfterSplice += + unresolved.spliceDelta

        dispatch(moveTaskGroup({
          count: unresolved.count,
          sliceFrom,
          indexToAfterSplice,
        }))
      }}
      onAddNextDate={() => dispatch(addDay({ index: getNextDayIndex() }))}
      onAddPreviousDate={() => dispatch(addDay({ index: getPrevDayIndex() }))}

      onSetMeasure={({ taskId, dayId, value }) => {
        dispatch(setMeasure({ taskId, dayId, value }))
        afterMeasure({ taskId, dayId, value })
      }}
      onIncMeasure={({ measure, taskId, dayId, value }) => {
        if (dayMap[dayId].index === maxDayIndex && measure.value == -value /* string compare */) {
          dispatch(clearMeasure(Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })))
        } else {
          dispatch(incMeasure({ taskId, dayId, value }))
        }
        if (settings.autoReward && (settings.rewardAnyDay || dayId === maxDayIndex)) {
          const reward = value * 1000 * 60 || 0
          dispatch(incTimer(reward))
        }
        afterMeasure({ taskId, dayId, value })
      }}
      onClearMeasure={({ taskId, dayId }) => dispatch(clearMeasure(Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })))}

      onRemoveDate={dayId => { window.confirm("r u sure?") && dispatch(removeDayAndMeasuresByDayId(dayId)) }}
      onSetAllNA={dayId => { window.confirm("r u sure?") && dispatch(setMeasuresForAllTasksByDayId({ dayId, value: m => m === undefined ? null : m })) }}
      onSetAllZeros={dayId => { window.confirm("r u sure?") && dispatch(setMeasuresForAllTasksByDayId({ dayId, value: m => m === undefined ? 0 : m })) }}
      onSetAllEmpty={dayId => { window.confirm("r u sure?") && dispatch(clearAllByDayId(dayId)) }}

      onChangeDate={dayId => /*setDateChangeModal({ show: true, dayId })*/ alert("NOT IMPLEMENTED")}
    />
  )
}