import { getDOWShortText } from "../utils/time";
import CustomButton from "./CustomButton";
import "./DateCell.scss"

export default function ({ value, onClick, className }) {
  console.debug("date redraw")

  const date = new Date(value);
  const dayOfMonth = date.getDate();
  const dow = getDOWShortText(date)

  return (
    <CustomButton className={`date-cell cell wd${date.getDay()} ${className}`} onClick={onClick}>
      <div className="dom">
        {dayOfMonth}
      </div>
      <div className="dow">
        {dow}
      </div>
    </CustomButton>
  );
}
