import React, { useState, createContext, useContext, useRef, useEffect } from "react"
import { moved } from "../utils/array"
import "./Sortable.scss"

const DragStartContext = createContext(null)

function findMaxBottomElement(containerRef) {
  const items = [...containerRef.current.querySelectorAll(".sortable-item-static")];

  if (items.length === 0) return { maxBottomElement: null, maxBottomValue: 0 };

  let maxBottomValue = 0;
  let maxBottomElement = null;

  items.forEach(el => {
    const bottom = el.getBoundingClientRect().bottom;
    if (bottom > maxBottomValue) {
      maxBottomValue = bottom;
      maxBottomElement = el;
    }
  });

  return { maxBottomElement, maxBottomValue };
}

export function Sortable({ style, children, onMove, onStart, onFinish }) {
  const [isDragging, setDragging] = useState(false)
  const [maybeDragging, setMaybeDragging] = useState(false)
  const [y1, setY1] = useState(null)

  const indexFromRef = useRef(null)
  const targetFromTopRef = useRef(null)
  const indexToRef = useRef(null)
  const y0Ref = useRef(null)
  // const [lastScrollTop, setLastScrollTop] = useState(0)

  const items = React.Children.toArray(children).filter(child => child.type === SortableItem)

  let showingItems = items
  let draggingStatus = new Array(items.length)

  if (
    isDragging &&
    // indexFromRef.current !== indexToRef.current &&
    indexFromRef.current !== null &&
    indexToRef.current !== null
  ) {
    draggingStatus[indexFromRef.current] = true
    showingItems = moved(items, indexFromRef.current, indexToRef.current)
    draggingStatus = moved(draggingStatus, indexFromRef.current, indexToRef.current)
  }

  function start(e, indexFrom) {
    const clientY = e.clientY
    const target = e.target

    if (target === undefined) {
      throw Error(`Target is undefined`)
    }

    if (clientY === undefined) {
      throw Error(`ClientY is undefined`)
    }

    y0Ref.current = clientY
    setY1(clientY)
    indexFromRef.current = indexFrom
    targetFromTopRef.current = target.closest('.sortable-item').getBoundingClientRect().y

    setMaybeDragging(true)
  }

  function finish() {

    if (!maybeDragging) {
      return
    }

    setMaybeDragging(false)

    if (!isDragging) {
      return
    }

    const i = indexFromRef.current
    const j = indexToRef.current

    setMaybeDragging(false)
    setDragging(false)
    indexFromRef.current = indexToRef.current = null

    if (i !== j && i !== null && j !== null) {
      if (onMove) {
        onMove(i, j)
      } else {
        console.warn("sortable: onMove is undefined")
      }
    }

    console.debug("sortable: finish")
    onFinish && onFinish()
  }

  const ref = useRef()
  const bottomInfoRef = useRef()

  function onPointerMove(e) {
    if (!maybeDragging) {
      return
    }

    const { clientX, clientY } = e

    if (!isDragging) {
      if (Math.abs(clientY - y0Ref.current) > 10) {
        setDragging(true)
        console.debug("sortable: start", e.target) //, target.closest('.sortable-item').getBoundingClientRect())
        onStart && onStart(e, indexFromRef.current)
        bottomInfoRef.current = null
      }
    }

    if (!isDragging) {
      return
    }

    setY1(clientY)

    if (bottomInfoRef.current === null) {
      bottomInfoRef.current = findMaxBottomElement(ref)
    }


    let target
    if (clientY > bottomInfoRef.current.maxBottomValue) {
      target = bottomInfoRef.current.maxBottomElement
    } else {
      const elements = document.elementsFromPoint(clientX, clientY).filter(el => el.classList.contains("sortable-item-static"))
      target = elements[0]
    }
    if (target) {
      const index = parseInt(target.dataset.index)
      if (!isNaN(index) && index !== indexToRef.current) {
        onHover(index)
      }
    }

    console.debug("sortable: onPointerMove")
  }

  function onHover(index) {
    if (!isDragging) {
      return
    }
    indexToRef.current = index
    console.debug("sortable: onHover", index)
  }

  function getItemStyle(index) {
    // if (isDragging && indexFromRef.current == index) {
    //     return {
    //         opacity: 0.25
    //     }
    // }
    if (isDragging && draggingStatus[index]) {
      return {
        opacity: 0
      }
    }
    return {}
  }

  return (
    <div className="sortable-wrapper" ref={ref}>
      <div
        className="sortable"
        onPointerUp={finish}
        onPointerCancel={finish}
        onPointerLeave={finish}
        onPointerMove={onPointerMove}
        style={{ touchAction: 'none', ...style }}
      >
        {
          showingItems.map((child, index) => {
            return (
              <div
                className="sortable-item sortable-item-static"
                key={index}
                data-index={index}
                onPointerEnter={e => onHover(index)}
                style={getItemStyle(index)}
              >
                {
                  React.cloneElement(child, {
                    onDragStart: e => {
                      // document.activeElement && document.activeElement.blur && document.activeElement.blur()
                      start(e, index)
                    },
                  })
                }
              </div>
            )
          })
        }
        {isDragging && indexFromRef.current !== null && (
          <div
            className="sortable-item"
            style={{
              position: 'absolute',
              top: targetFromTopRef.current + y1 - y0Ref.current + document.documentElement.scrollTop + 'px',
              pointerEvents: 'none',
            }}
          >
            {
              React.cloneElement(items[indexFromRef.current])
            }
          </div>
        )}
      </div>
    </div>
  )
}


export function SortableItem({ children, onDragStart }) {
  return (
    <DragStartContext.Provider value={e => onDragStart(e)}>
      <div className="sortable-item">
        {children}
      </div>
    </DragStartContext.Provider>
  )
}

export function SortableDragHandler({ children }) {
  const onDragStart = useContext(DragStartContext)

  return (
    <div
      className="sortable-drag-handler no-pointer-draggable"
      onPointerDown={e => onDragStart(e)}
      style={{ display: "inline-block", userSelect: 'none', cursor: 'grab' }}
    >
      {children}
    </div>
  )
}
