import React, { useContext, useEffect, useRef, useState } from "react";
import { findAllParentsWithClass, findParentWithClass, getAllParents, hasParentWithClass } from "../utils/html";
import "./Pointer.scss"

const PointerContext = React.createContext(null)

const getPointFromEvent = e => ({ x: e.clientX, y: e.clientY })
const getPointDiff = (a, b) => ({ x: a.x - b.x, y: a.y - b.y })
const ZERO = { x: 0, y: 0 }

function initItem(el) {
  const item = {
    el,
    // dontLeave: el.dataset.dontLeave === "true",
    // dontCancel: el.dataset.dontCancel === "true",
    minXOffset: parseInt(el.dataset.minXOffset) || 0,
    maxYOffset: parseInt(el.dataset.maxYOffset) || 0,
    minYOffset: parseInt(el.dataset.minYOffset) || 0,
    maxXOffset: parseInt(el.dataset.maxXOffset) || 0,
    xFailed: false,
    yFailed: false,
  }

  return item
}

function isReadyToDrag({
  minXOffset,
  maxYOffset,
  minYOffset,
  maxXOffset,
  xFailed,
  yFailed,
}, offset) {

  if (maxYOffset && yFailed) {
    return false
  }

  if (maxXOffset && xFailed) {
    return false
  }

  if (minXOffset && Math.abs(offset.x) < minXOffset) {
    return false
  }

  if (minYOffset && Math.abs(offset.y) < minYOffset) {
    return false
  }

  return true
}

function findFinalElement(items, offset) {
  for (const item of items) {

    if (item.maxYOffset && !item.yFailed && Math.abs(offset.y) > item.maxYOffset) {
      item.yFailed = true
    }

    if (item.maxXOffset && !item.xFailed && Math.abs(offset.x) > item.maxXOffset) {
      item.xFailed = true
    }

    if (isReadyToDrag(item, offset)) {
      return item.el
    }
  }

  return null
}

export function PointerProvider({ children }) {

  useEffect(() => {

    let pointedItems
    let isPointerDown
    let offset
    let startPoint
    let currentPoint
    let finalElement

    function handlePointerDown(e) {
      
      const elementFromPoint = document.elementFromPoint(e.clientX, e.clientY)
      
      if (hasParentWithClass(elementFromPoint, "no-pointer-draggable")) {
        return
      }
      
      pointedItems = findAllParentsWithClass(elementFromPoint, "watch-pointer").map(el => initItem(el))

      console.debug("watch-pointer", pointedItems.map(it => it.el), pointedItems)

      if (pointedItems.length === 0) {
        return
      }

      console.debug("handlePointerDown")

      isPointerDown = true
      offset = ZERO
      startPoint = currentPoint = getPointFromEvent(e)
      finalElement = null
    }

    function handlePointerMove(e) {
      if (!isPointerDown) {
        return
      }

      // console.debug("handlePointerMove")

      const p = getPointFromEvent(e)
      currentPoint = p
      offset = getPointDiff(currentPoint, startPoint)

      if (finalElement) {

        finalElement.dispatchEvent(new CustomEvent("my-pointer-drag-move", {
          detail: {
            startPoint,
            currentPoint,
            offset,
          }
        }))

      } else {

        finalElement = findFinalElement(pointedItems, offset)

        if (finalElement) {
          console.debug("watch-pointer found final element", finalElement)

          finalElement.dispatchEvent(new CustomEvent("my-pointer-drag-start", {
            detail: {
              startPoint,
              currentPoint,
              offset,
            }
          }))
        }
      }
    }

    function handleQuit() {
      isPointerDown = false
      pointedItems = []
      if (finalElement) {
        finalElement.dispatchEvent(new CustomEvent("my-pointer-drag-end", {
          detail: {
            startPoint,
            currentPoint,
            offset,
          }
        }))
        finalElement = null
      }
    }

    function handlePointerUp(e) {
      if (!isPointerDown) {
        return
      }

      console.debug("handlePointerUp")
      handleQuit()
    }

    function handlePointerLeave(e) {
      if (!isPointerDown) {
        return
      }

      console.debug("pointer leave")
      handleQuit()
    }

    function handlePointerCancel(e) {
      if (!isPointerDown) {
        return
      }

      console.debug("pointer cancel")
      handleQuit()
    }

    window.addEventListener("pointerdown", handlePointerDown)
    window.addEventListener("pointermove", handlePointerMove)
    window.addEventListener("pointerup", handlePointerUp)
    window.addEventListener("pointercancel", handlePointerCancel)
    window.addEventListener("pointerleave", handlePointerLeave)

    return () => {
      window.removeEventListener("pointerdown", handlePointerDown)
      window.removeEventListener("pointermove", handlePointerMove)
      window.removeEventListener("pointerup", handlePointerUp)
      window.removeEventListener("pointercancel", handlePointerCancel)
      window.removeEventListener("pointerleave", handlePointerLeave)
    }
  }, [])


  return (
    <PointerContext.Provider value={{
    }}>
      {children}
    </PointerContext.Provider>
  )
}

function usePointerContext() {
  return useContext(PointerContext)
}

export function Draggable({
  children,
  // dontLeave,
  // dontCancel,
  minXOffset,
  maxYOffset,
  minYOffset,
  maxXOffset,
  onDragStart = () => {},
  onDragEnd = () => {},
  onDragMove = () => {},
}) {
  const ref = useRef()
  const handlers = useRef()
  handlers.current = {
    onDragStart,
    onDragEnd,
    onDragMove,
  }

  useEffect(() => {
    if (!ref.current) {
      return
    }

    const el = ref.current

    function handleDragStart(e) {
      handlers.current.onDragStart(e)
    }

    function handleDragMove(e) {
      handlers.current.onDragMove(e)
    }

    function handleDragEnd(e) {
      handlers.current.onDragEnd(e)
    }

    el.addEventListener("my-pointer-drag-start", handleDragStart)
    el.addEventListener("my-pointer-drag-move", handleDragMove)
    el.addEventListener("my-pointer-drag-end", handleDragEnd)

    return () => {
      el.removeEventListener("my-pointer-drag-start", handleDragStart)
      el.removeEventListener("my-pointer-drag-move", handleDragMove)
      el.removeEventListener("my-pointer-drag-end", handleDragEnd)
    }
  }, [])

  return (
    <div
      ref={ref}
      className="watch-pointer"
      // data-dont-leave={dontLeave ? "true" : ""}
      // data-dont-cancel={dontCancel ? "true" : ""}
      data-min-x-offset={minXOffset || 0}
      data-max-y-offset={maxYOffset || 0}
      data-min-y-offset={minYOffset || 0}
      data-max-x-offset={maxXOffset || 0}
      style={{ touchAction: "none" }}
    >
      {children}
    </div>
  )
}
