import React, { useState, useRef } from 'react'
import CustomInput from './CustomInput'
import CustomButton from './CustomButton'
import "./LongClickInput.scss"

export default function LongClickInput({
  value,
  onChange,
  ...args
}) {
  const [isInputVisible, setInputVisible] = useState(false);
  const containerRef = useRef(null); // Ref for the container

  // Handle key down event to blur the input on Enter press
  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault(); // Prevent the default form submit behavior
      const inputElement = containerRef.current.querySelector('input');
      if (inputElement) {
        inputElement.blur(); // Blur the input field
      }
      // setInputVisible(false)
    }
  }

  return (
    <div ref={containerRef} className='long-click-input' {...args}>
      {
        isInputVisible
          ? <CustomInput
            autoFocus
            onBlur={() => setInputVisible(false)}
            onChange={(e) => onChange(e.target.value)}
            onKeyDown={handleKeyDown}
            value={value}
          />
          : <CustomButton onContextMenu={(e) => {
            e.preventDefault(); // Prevent the context menu from showing
            setInputVisible(true);
          }}>
            {value}
          </CustomButton>
      }
    </div>
  )
}
