import { useDispatch, useSelector } from "react-redux"
import { MEASURE_TYPE_EMPTY, MEASURE_TYPE_NA, MEASURE_TYPE_NUMBER, Measure } from "../models/Measure"

const DAY = 60 * 60 * 24 * 1000
const toList = map => Object.values(map).sort((a, b) => a.index - b.index)

export function useMeasures() {
  const startTime = useSelector(state => state.system.startTime)
  const measureMap = useSelector(state => state.measureMap)
  const dayMap = useSelector(state => state.dayMap)
  const dayIndexToTime = index => index * DAY + startTime
  const days = toList(dayMap).map(o => ({ ...o, time: dayIndexToTime(o.index) }))
  const maxDayIndex = days.length > 0 ? days[days.length - 1].index : null

  function getMeasureValueByTaskIdAndDayId(taskId, dayId) {
    const m = measureMap[Measure.getMeasureIdByTaskIdAndDayId({ taskId, dayId })]
    const d = dayMap[dayId]
    const isToday = d.index === maxDayIndex

    if (m === undefined) {
      if (isToday) {
        return { type: MEASURE_TYPE_EMPTY }
      } else {
        return { type: MEASURE_TYPE_NA }
      }
    }

    return m
  }

  return {
    getMeasureValueByTaskIdAndDayId
  }
}