import React, { useRef, useEffect, useState } from 'react'

export const RotatingSegment2 = ({ image1, image2, lineAngle, segmentAngle }) => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    const img1 = new Image();
    const img2 = new Image();
    img1.src = image1;
    img2.src = image2;

    img1.onload = () => {
      img2.onload = () => {
        drawImages(context, img1, img2);
      };
    };

    const drawImages = (ctx, img1, img2) => {
      const width = canvas.width;
      const height = canvas.height;
      const centerX = width / 2;
      const centerY = height / 2;

      // Draw the first image covering the entire canvas
      ctx.drawImage(img1, 0, 0, width, height);

      // Set up the clipping region for the segment
      ctx.save();
      ctx.beginPath();
      ctx.moveTo(centerX, centerY);
      ctx.arc(centerX, centerY, Math.min(centerX, centerY) * 2, (lineAngle * Math.PI) / 180, ((lineAngle + segmentAngle) * Math.PI) / 180);
      ctx.closePath();
      ctx.clip();

      // Draw the second image within the clipping region
      ctx.drawImage(img2, 0, 0, width, height);
      ctx.restore();
    };
  }, [image1, image2, lineAngle, segmentAngle]);

  return (
    <canvas ref={canvasRef} width={500} height={500} />
  );
}

export function DayNightCircle({ angles, ...canvas }) {
  const images = [
    process.env.PUBLIC_URL + "/image4.png",
    process.env.PUBLIC_URL + "/image1.png",
    process.env.PUBLIC_URL + "/image2.png",
    process.env.PUBLIC_URL + "/image3.png",
    process.env.PUBLIC_URL + "/image4.png",
    process.env.PUBLIC_URL + "/image1.png",
    process.env.PUBLIC_URL + "/image2.png",
    process.env.PUBLIC_URL + "/image3.png",
  ]

  return <SemisphereDayNightSegments
    images={images}
    angles={angles}
    transitionWidth={1}
    {...canvas}
  />
}

export const SemisphereDayNightSegments = ({ images, angles, transitionWidth, ...canvasArgs }) => {
  const canvasRef = useRef(null)
  const cachedImagesRef = useRef({})
  const [redrawCount, setRedrawCount] = useState(0)
  const lastData = useRef()

  useEffect(() => {
    const data = [images, angles, transitionWidth, document.body, canvasRef.current?.height]

    if (JSON.stringify(data) !== lastData.current) {
      setRedrawCount(count => count + 1)
      lastData.current = JSON.stringify(data)
    }
  }, [images, angles, transitionWidth, document.body, canvasRef.current?.height])

  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    const loadImage = (src) => {
      return new Promise((resolve) => {
        if (cachedImagesRef.current[src]) {
          resolve(cachedImagesRef.current[src]);
        } else {
          const img = new Image();
            console.debug("loading", src)
            img.src = src;
          img.onload = () => {
            cachedImagesRef.current[src] = img;
            resolve(img);
          };
        }
      });
    };

    const loadImages = async () => {
      const loadedImages = await Promise.all(images.map(src => loadImage(src)));
      drawImages(context, loadedImages, angles, transitionWidth);
    };

    const drawImages = (ctx, imageElements, angles, transitionWidth) => {
      console.debug("redraw")

      const width = canvas.width;
      const height = canvas.height;
      const centerX = width / 2;
      const centerY = height / 2;

      // Draw each image segment
      imageElements.forEach((image, index) => {
        ctx.save();
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);
        ctx.arc(centerX, centerY, Math.min(centerX, centerY), (angles[index] * Math.PI) / 180, ((angles[(index + 1) % angles.length]) * Math.PI) / 180);
        ctx.closePath();
        ctx.clip();

        ctx.drawImage(image, 0, 0, width, image.height / image.width * width);
        ctx.restore();
      });

      // Draw blend transitions between segments
      for (let i = 0; i < angles.length; i++) {
        const currentAngle = angles[i];
        const nextIndex = (i + 1) % angles.length;
        const nextAngle = angles[nextIndex];
        blendSegmentBorders(ctx, width, height, centerX, centerY, currentAngle, nextAngle, transitionWidth, imageElements[i], imageElements[nextIndex]);
      }
    };

    const blendSegmentBorders = (ctx, width, height, centerX, centerY, startAngle, endAngle, transitionWidth, leftImage, rightImage) => {
      const startRadians = (startAngle * Math.PI) / 180;
      const endRadians = (endAngle * Math.PI) / 180;

      for (let t = 0; t < transitionWidth; t++) {
        const angle = startRadians + (t / transitionWidth) * (endRadians - startRadians);
        const weight = t / transitionWidth;

        const x1 = centerX + Math.cos(angle) * Math.min(centerX, centerY);
        const y1 = centerY + Math.sin(angle) * Math.min(centerX, centerY);

        const leftPixelData = getImagePixelData(leftImage, width, height, x1, y1);
        const rightPixelData = getImagePixelData(rightImage, width, height, x1, y1);

        const blendedPixel = blendPixels(leftPixelData, rightPixelData, weight);

        ctx.fillStyle = `rgba(${blendedPixel[0]}, ${blendedPixel[1]}, ${blendedPixel[2]}, ${blendedPixel[3] / 255})`;
        ctx.fillRect(x1, y1, 1, 1);
      }
    };

    const getImagePixelData = (image, canvasWidth, canvasHeight, x, y) => {
      const offscreenCanvas = document.createElement('canvas');
      offscreenCanvas.width = canvasWidth;
      offscreenCanvas.height = canvasHeight;
      const offscreenCtx = offscreenCanvas.getContext('2d');

      offscreenCtx.drawImage(image, 0, 0, canvasWidth, canvasHeight);

      const imageData = offscreenCtx.getImageData(x, y, 1, 1);
      return imageData.data;
    };

    const blendPixels = (leftPixel, rightPixel, weight) => {
      const r = leftPixel[0] * (1 - weight) + rightPixel[0] * weight;
      const g = leftPixel[1] * (1 - weight) + rightPixel[1] * weight;
      const b = leftPixel[2] * (1 - weight) + rightPixel[2] * weight;
      const a = leftPixel[3] * (1 - weight) + rightPixel[3] * weight;
      return [r, g, b, a];
    };

    loadImages();
  }, [redrawCount])

  return (
    <canvas ref={canvasRef} {...canvasArgs} />
  );
};


export const CircularDayNightSegments = ({ images, angles, transitionWidth, ...canvasArgs }) => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    const imageElements = images.map((src) => {
      const img = new Image();
      img.src = src;
      return img;
    });

    Promise.all(imageElements.map(img => new Promise(resolve => img.onload = resolve)))
      .then(() => {
        drawImages(context, imageElements, angles, transitionWidth);
      });

    const drawImages = (ctx, imageElements, angles, transitionWidth) => {
      const width = canvas.width;
      const height = canvas.height;
      const centerX = width / 2;
      const centerY = height / 2;

      // Draw each image segment
      imageElements.forEach((image, index) => {
        ctx.save();
        ctx.beginPath();
        ctx.moveTo(centerX, centerY);
        ctx.arc(centerX, centerY, Math.min(centerX, centerY), (angles[index] * Math.PI) / 180, ((angles[(index + 1) % angles.length]) * Math.PI) / 180);
        ctx.closePath();
        ctx.clip();

        ctx.drawImage(image, 0, 0, width, height);
        ctx.restore();
      });

      // Draw blend transitions between segments
      for (let i = 0; i < angles.length; i++) {
        const currentAngle = angles[i];
        const nextIndex = (i + 1) % angles.length;
        const nextAngle = angles[nextIndex];
        blendSegmentBorders(ctx, width, height, centerX, centerY, currentAngle, nextAngle, transitionWidth, imageElements[i], imageElements[nextIndex]);
      }
    };

    const blendSegmentBorders = (ctx, width, height, centerX, centerY, startAngle, endAngle, transitionWidth, leftImage, rightImage) => {
      const startRadians = (startAngle * Math.PI) / 180;
      const endRadians = (endAngle * Math.PI) / 180;

      for (let t = 0; t < transitionWidth; t++) {
        const angle = startRadians + (t / transitionWidth) * (endRadians - startRadians);
        const weight = t / transitionWidth;

        const x1 = centerX + Math.cos(angle) * Math.min(centerX, centerY);
        const y1 = centerY + Math.sin(angle) * Math.min(centerX, centerY);

        const leftPixelData = getImagePixelData(leftImage, width, height, x1, y1);
        const rightPixelData = getImagePixelData(rightImage, width, height, x1, y1);

        const blendedPixel = blendPixels(leftPixelData, rightPixelData, weight);

        ctx.fillStyle = `rgba(${blendedPixel[0]}, ${blendedPixel[1]}, ${blendedPixel[2]}, ${blendedPixel[3] / 255})`;
        ctx.fillRect(x1, y1, 1, 1);
      }
    };

    const getImagePixelData = (image, canvasWidth, canvasHeight, x, y) => {
      const offscreenCanvas = document.createElement('canvas');
      offscreenCanvas.width = canvasWidth;
      offscreenCanvas.height = canvasHeight;
      const offscreenCtx = offscreenCanvas.getContext('2d');

      offscreenCtx.drawImage(image, 0, 0, canvasWidth, canvasHeight);

      const imageData = offscreenCtx.getImageData(x, y, 1, 1);
      return imageData.data;
    };

    const blendPixels = (leftPixel, rightPixel, weight) => {
      const r = leftPixel[0] * (1 - weight) + rightPixel[0] * weight;
      const g = leftPixel[1] * (1 - weight) + rightPixel[1] * weight;
      const b = leftPixel[2] * (1 - weight) + rightPixel[2] * weight;
      const a = leftPixel[3] * (1 - weight) + rightPixel[3] * weight;
      return [r, g, b, a];
    };
  }, [images, angles, transitionWidth]);

  return (
    <canvas ref={canvasRef} {...canvasArgs} />
  );
};
