import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';
import { Provider, useDispatch } from 'react-redux';
import React, { useEffect, useState } from 'react';
import TablePage from './pages/TablePage';
import TestPage from './pages/TestPage';
import "./App.scss"
import SettingsPage from './pages/SettingsPage';
import { fetchSystemTime, migrateStorage } from './store';

export default function App() {
  const [migrated, setMigrated] = useState(false) 

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(migrateStorage()).unwrap().catch(e => console.error(e))
    setMigrated(true)

    // const interval = setInterval(() => {
    //   dispatch(fetchSystemTime())
    // }, 1000)

    // return () => clearInterval(interval)
  }, [])

  return (
    migrated
    ?
    <BrowserRouter>
      <Routes>
        <Route path={`/`} element={<TablePage />} />
        <Route path={`${process.env.PUBLIC_URL}/`} element={<TablePage />} />
        <Route path={`${process.env.PUBLIC_URL}/test`} element={<TestPage />} />
        <Route path={`${process.env.PUBLIC_URL}/settings`} element={<SettingsPage />} />
        <Route path="*" element={<NotFound />} />
      </Routes>
    </BrowserRouter>
    :
    <></>
  );
}
function NotFound() {
  return <div>404: Page Not Found</div>;
}
