import CustomButton from "./CustomButton";
import DragHandler from "./DragHandler";
import { SortableDragHandler } from "./Sortable";
import CustomInput from "./CustomInput";
import "./GroupHeader.scss"
import LongClickInput from "./LongClickInput"

/*
https://www.w3schools.com/charsets/ref_utf_geometric.asp
      
    
*/

export default function ({
  isCollapsed,
  onCollapse,
  onTitleChange,
  title,
  onAddNewTask,
}) {
  return (
    <div className="group-row">
      <div className="collapse drag-wrapper">
        <SortableDragHandler>
          <CustomButton onClick={onCollapse}>
            {
              isCollapsed
              &&
              ""
              ||
              ""
            }
          </CustomButton>
        </SortableDragHandler>
      </div>
      <div className="title">
        <LongClickInput value={title} onChange={value => onTitleChange(value)} />
      </div>
      {
        isCollapsed
        ||
        <div className="add-new">
          <CustomButton onClick={onAddNewTask}>
            
          </CustomButton>
        </div>
      }
    </div>
  )
}