export default function useElementWatcher(nodeRef, onResize, timeInterval) {
    const observerRef = useRef()
    const [scrollWidth, setScrollWidth] = useState(0)

    useEffect(() => {
        // Function to call the callback with current element
        const maybeScrollResize = () => {
            if (nodeRef.current) {
                const currentScrollWidth = nodeRef.current.scrollWidth
                if (scrollWidth !== currentScrollWidth) {
                    setScrollWidth(currentScrollWidth)
                    onResize(nodeRef.current)
                }
            }
        }

        // Initialize ResizeObserver
        if (observerRef.current) {
            observerRef.current.disconnect()
        }
        if (nodeRef.current) {
            observerRef.current = new ResizeObserver(() => nodeRef.current && onResize(nodeRef.current))
            observerRef.current.observe(nodeRef.current)
        }

        // Set up interval for checking scrollWidth
        const intervalId = setInterval(maybeScrollResize, timeInterval) // Check every 200 ms

        return () => {
            if (observerRef.current) {
                observerRef.current.disconnect()
            }
            clearInterval(intervalId) // Clean up the interval on unmount
        }
    }, [nodeRef, onResize, scrollWidth])

    // Returning something from the hook if needed
}
