import { useCallback, useRef, useState } from "react";
import "./Constrained.scss"

export default function ({
    children,
    scrollLeft,
    scrollTop,
    className,
    width,
    height,
    onNodeUpdate = () => { },
    ...args
}) {
    const ref = useRef()

    const onNodeUpdate2 = node => {
        if (!node) {
            return
        }
        onNodeUpdate(node)
        ref.current = node
        if (node) {
            if (typeof scrollLeft === "number") {
                node.scrollLeft = scrollLeft
            }
            if (typeof scrollTop === "number") {
                node.scrollTop = scrollTop
            }
        }
    }

    useState(() => {
        if (ref.current) {
            if (typeof scrollLeft === "number") {
                ref.current.scrollLeft = scrollLeft
            }
        }
    }, [scrollLeft])

    useState(() => {
        if (ref.current) {
            if (typeof scrollTop === "number") {
                ref.current.scrollTop = scrollTop
            }
        }
    }, [scrollTop])

    const style = {}

    if (width !== undefined) {
        style.width = width + "px"
    }

    if (height !== undefined) {
        style.height = height + "px"
    }

    return (
        <div
            className={`constrained ${className}`}
            ref={onNodeUpdate2}
            style={style}
            {...args}
        >
            {children}
        </div>
    )

}
