import { useRef, useState } from "react"
import Constrained from "./Constrained"
import { Draggable } from "./Pointer"

// General function to limit scrolling to the bounds of the element
function limitScroll(el, scrollValue, isVertical = false) {
  const maxScroll = isVertical ? el.scrollHeight - el.offsetHeight : el.scrollWidth - el.offsetWidth
  return Math.max(0, Math.min(scrollValue, maxScroll))
}

export default function ({
  children,
  scrollLeft = 0,
  scrollTop = 0,
  onScrollLeft = () => { },
  onScrollTop = () => { },
  onStartScroll = () => { },
  onStopScroll = () => { },
  filterTouches = null,
  minXOffset,
  minYOffset,
  maxXOffset,
  maxYOffset,
  ...args
}) {
  const ref = useRef()
  const [p, setP] = useState(null)

  function handleDragStart() {
    const newP = {
      scrollLeft: limitScroll(ref.current, scrollLeft,),
      scrollTop: limitScroll(ref.current, scrollTop, true),
    }
    setP(newP)
    onStartScroll(newP)
  }

  function handleDragMove({ detail: { offset } }) {
    onScrollLeft(limitScroll(ref.current, p.scrollLeft - offset.x,))
    onScrollTop(limitScroll(ref.current, p.scrollTop - offset.y, true))
  }

  function handleDragEnd({ detail: { offset } }) {
    onStopScroll({
      scrollLeft: p.scrollLeft - offset.x,
      scrollTop: p.scrollTop - offset.y,
    })
  }

  function onNodeUpdate(node) {
    if (node) {
      ref.current = node
    }
  }

  return (
    <Constrained
      scrollLeft={scrollLeft}
      scrollTop={scrollTop}
      onNodeUpdate={onNodeUpdate}
      {...args}
    >
      <Draggable
        minXOffset={minXOffset}
        minYOffset={minYOffset}
        maxXOffset={maxXOffset}
        maxYOffset={maxYOffset}
        onDragStart={handleDragStart}
        onDragMove={handleDragMove}
        onDragEnd={handleDragEnd}
      >
        {children}
      </Draggable>
    </Constrained>
  )
}