import React, { useState } from 'react';
import './Accordion.css';
import { Draggable } from './Pointer';
// import useDrag from '../hooks/useDrag';

const Accordion = ({ children }) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [offsetX, setOffsetX] = useState(0)

  return (
    <Draggable
      minXOffset={50}
      onDragStart={() => {
        setOffsetX(0)
      }}
      onDragMove={({ detail: { offset } }) => {
        setOffsetX(offset.x)
      }}
      onDragEnd={() => {
        if (offsetX > 0 && activeIndex > 0) {
          // Swiped right
          setActiveIndex((prev) => prev - 1);
        } else if (offsetX < 0 && activeIndex < children.length - 1) {
          // Swiped left
          setActiveIndex((prev) => prev + 1);
        }
        setOffsetX(0)
      }}
    >
      <div className="accordion-container">
        <div
          className="accordion-content"
          style={{
            transform: `translateX(calc(${-activeIndex * 100}% + ${offsetX}px))`,
            transition: offsetX ? 'none' : 'transform 0.3s ease',
          }}
        >
          {React.Children.map(children, (child, index) => (
            <div className="accordion-item" key={index}>
              {child}
            </div>
          ))}
        </div>
      </div>
    </Draggable>
  );
};

export default Accordion;
