import React, { useState } from 'react';
import "./Swipeable.scss"
import { hasParentWithClass } from '../utils/html';
import { Draggable } from './Pointer';

const cumsum = (arr) => {
  let result = [];
  arr.reduce((a, b, i) => result[i] = a + b, 0);
  return result;
}

const reversed = (arr) => {
  let result = []
  for (let i = arr.length - 1; i >= 0; i--) {
    result.push(arr[i])
  }
  return result
}

export function Swipeable({
  children
}) {
  if (!(children instanceof Array)) {
    children = [children]
  }

  const leftActions = React.Children.toArray(children).filter(child => child.type === SwipeableLeftAction);
  const rightActions = React.Children.toArray(children).filter(child => child.type === SwipeableRightAction);

  const leftCumSum = cumsum(leftActions.map(a => a.props.width));
  const rightCumSum = cumsum(reversed(rightActions).map(a => a.props.width))

  const swipeContent = children.find(child => child && child.type !== SwipeableLeftAction && child.type !== SwipeableRightAction)

  let leftActionIndex = null;
  let rightActionIndex = null;

  const [offsetX, setOffsetX] = useState(0)

  function handleDragMove({ detail: { offset } }) {
    setOffsetX(offset.x)
  }

  function handleDragEnd() {
    setOffsetX(0)

    if (leftActionIndex !== null) {
      leftActions[leftActionIndex].props.onAction();
      return;
    }

    if (rightActionIndex !== null) {
      rightActions[rightActionIndex].props.onAction();
      return
    }
  }

  let swipeableClassOn = ""
  if (offsetX > 0) {
    swipeableClassOn = "swipeable-left-on"
  } else if (offsetX < 0) {
    swipeableClassOn = "swipeable-right-on"
  }

  if (offsetX > 0) {
    for (let i = 0; i < leftCumSum.length; i++) {
      if (offsetX > leftCumSum[i]) {
        leftActionIndex = i;
      } else {
        break;
      }
    }
  } else {
    for (let i = 0; i < rightCumSum.length; i++) {
      if (-offsetX > rightCumSum[i]) {
        rightActionIndex = rightCumSum.length - i - 1;
      } else {
        break;
      }
    }
  }

  return (
    <div
      className={`swipeable ${swipeableClassOn}`}
    >
      <div className="left-actions">
        {
          leftActions.map((action, index) => {
            const props = {}
            if (leftActionIndex === index) {
              props.className = "selected";
            }
            return <div {...props} key={index}>{action}</div>;
          })
        }
      </div>

      <div className='right-actions'>
        {
          rightActions.map((action, index) => {
            const props = {}
            if (rightActionIndex === index) {
              props.className = "selected";
            }
            return <div {...props} key={index}>{action}</div>;
          })
        }
      </div>

      <Draggable
        minXOffset={10}
        maxYOffset={10}
        onDragMove={handleDragMove}
        onDragEnd={handleDragEnd}
      >
        <div className='swipe-content' style={{
          transform: `translateX(${offsetX}px)`,
        }}>
          {swipeContent}
        </div>
      </Draggable>
    </div>
  )
}


export function SwipeableRightAction({ width, children }) {
  return (
    <div className='swipeable-right-action' style={{
      width: width + 'px',
    }}>
      {children}
    </div>
  )
}

export function SwipeableLeftAction({ width, children }) {
  return (
    <div
      className='swipeable-left-action'
      style={{
        width: width + 'px',
      }}
    >
      {children}
    </div>
  )
}
