import CustomButton from "./CustomButton";
import "./StatCell.scss"

export default function ({
  className,
  value,
}) {
  console.debug("stat redraw")

  const _value = parseFloat(value)
  const isLte30 = !isNaN(_value) && _value <= 30 && 'is-lte-30'
  const isLte60 = !isNaN(_value) && _value <= 60 && 'is-lte-60'
  const isZero = !isNaN(_value) && _value == 0 && 'is-zero'

  return (
    <div className={`stat-cell ${className} ${isLte30} ${isLte60} ${isZero}`}>
      <CustomButton>
        {value}
      </CustomButton>
    </div>
  )
}